<script>
import { dateFormat } from '@/utils/utils'
export default {
  filters: {
    dateFilter (val) {
      return dateFormat(val)
    }
  },
  data () {
    return {
    }
  },
  computed: {
    currImTitleInfo () {
      return this.$store.state.imchat.currImTitleInfo
    },
    studentJson () {
      return this.currImTitleInfo.studentJson || {}
    },
    diagnosisContent () {
      return this.currImTitleInfo.diagnosisContent || {}
    },
    crmRoleId () {
      return this.$store.state.roleRelativeData.userInfo.crmRoleId
    }
  },
  methods: {
    goCustomerDetail () {
      this.$emit('goCustomerDetail', this.currImTitleInfo)
    },
    diagnosisHandle () {
      this.$emit('diagnosisHandle', this.currImTitleInfo)
    }
  }
}
</script>

