export default {
  getMsgReceiptTime: state => () => {
    if (
      !state.currSessionId ||
      !state.sessionMap[state.currSessionId] ||
      !state.sessionMap[state.currSessionId].msgReceiptTime
    ) {
      let msgsLen = state.currSessionMsgs.length - 1
      let msgReceiptTime = 0
      for (let i = msgsLen; i >= 0; i--) {
        let currMsg = state.currSessionMsgs[i]
        if (currMsg.flow === 'in') {
          msgReceiptTime = currMsg.time
          break
        }
      }
      return msgReceiptTime
    }
    return state.sessionMap[state.currSessionId].msgReceiptTime
  }
}
