import axios from '@/libs/api.request'

/**
 * @曹伟伟 http://apidoc.speiyou.cn/api/view/18730#
 *
 * 盘古-试听申请列表
 *
*/

export function getApplyList (params) {
  return axios.request({
    url: 'appointmentApply/condition',
    method: 'post',
    data: params
  })
}

