// import axios from '@/libs/api.request'
import { getLastIdentity, myIdentity } from '@/api/crm'
import { getEmpDeptRolesByEmpNo, getUserResourceUrlList } from '@/api/auth'
import Vue from 'vue'
const state = () => ({
  roleList: [],
  userInfo: {},
  roleInfo: {},
  allowButtonList: [], // 角色 按钮级权限
  roleIds: [ 2, 3, 4, 5 ] // 服务中心角色 城市经理、区域经理、服务中心主管、顾问
})
const mutations = {
  setRoleList (state, data) {
    state.roleList = data
  },
  setUserInfo (state, data) {
    state.userInfo = data
  },
  setRoleInfo (state, data) {
    state.roleInfo = data
  },
  setAllowButtonList (state, allowButtonList) {
    state.allowButtonList = allowButtonList
  }
}

const actions = {
  async gainRoleList (ctx) {
    // const { data } = { 'success': true, 'code': 0, 'msg': '操作成功', 'data': { 'currentTime': '2019-08-26 16:51:33', 'activeIdentityId': 111780, 'list': [{ 'identityId': 111780, 'userId': 2496, 'userName': '李帅', 'userWorkCode': '076757', 'departId': 252, 'departFullName': '培优事业部-教务-北京-海淀区-中关村--海淀黄庄', 'departIdList': [1, 2, 10, 14, 60, 252], 'departName': '海淀黄庄', 'crmRoleId': 5, 'roleName': '家庭顾问', 'createTime': 1566809474, 'createTimeMsg': '2019-08-26 16:51:14', 'status': 1, 'statusDisplay': '启用', 'remark': '', 'rank': 1, 'editable': true, 'deleteable': false }, { 'identityId': 111772, 'userId': 2496, 'userName': '李帅', 'userWorkCode': '076757', 'departId': 1, 'departFullName': '好未来教育科技集团', 'departIdList': [1], 'departName': '好未来教育科技集团', 'crmRoleId': 1, 'roleName': '超级管理员', 'createTime': 1563892022, 'createTimeMsg': '2019-07-23 22:27:02', 'status': 1, 'statusDisplay': '启用', 'remark': '', 'rank': 1, 'editable': true, 'deleteable': false }] } }
    // ctx.commit('setRoleList', data)
    let req = {
      // empNo: JSON.parse(Cookie.get('columbusUserInfo')).id
    }
    try {
      /**
       * 获取切换权限列表，显示在页面的右上角
       */
      const roleList = await getEmpDeptRolesByEmpNo(req)
      ctx.commit('setRoleList', roleList)

      /**
       * 获取当前用户最后一次登录的身份，如果初次登录，则后台必定返回一个身份
       */
      let userInfo = await getLastIdentity(req)
      // 后端确认getLastIdentity必定会返回一个权限信息，所以暂时不用前端匹配。
      // if (userInfo && userInfo.authIdentityId) {
      //   const list = roleList.filter(obj => obj.identityId === userInfo.authIdentityId)
      //   if (list.length === 0) {
      //     userInfo = roleList[0]
      //   } else {
      //     userInfo = list[0]
      //   }
      // }
      ctx.commit('setUserInfo', userInfo)
      if (userInfo.authIdentityId) {
        localStorage.setItem('authIdentityId', userInfo.authIdentityId)
      }
      const allowButtonList = await getUserResourceUrlList({
        type: 2,
        identityId: userInfo.authIdentityId
      })

      if (allowButtonList) {
        ctx.commit('setAllowButtonList', allowButtonList || [])
      }

      const resolveData = {
        roleList,
        latestRole: userInfo
      }

      return resolveData
    } catch (err) {
      Vue.prototype.$Message.error({
        content: '获取权限列表失败：' + err.message
      })
    }
  },
  async gainRoleInfo (ctx) {
    try {
      /**
       * 获取im相关的参数
       */
      const roleInfo = await myIdentity()
      ctx.commit('setRoleInfo', roleInfo)
    } catch (err) {
      Vue.prototype.$Message.error({
        content: '获取权限信息失败：' + err.message
      })
    }
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
