import { getClassRoster, queryAllSchool, getSchoolData, getGradeData, getClassData } from '@/api/schoolDic'
import { Message } from 'view-design'
const actions = {
  querySchoolData ({ commit }, params) {
    commit('setSchoolDataLoading', true)
    commit('setSchoolDataSearch', params)
    getSchoolData(params).then(data => {
      commit('setSchoolData', data)
    }).catch(err => {
      Message.error(err.message)
    }).finally(() => {
      commit('setSchoolDataLoading', false)
    })
  },
  queryGradeData ({ commit }, params) {
    commit('setGradeDataLoading', true)
    commit('setGradeDataSearch', params)
    getGradeData(params).then(data => {
      commit('setGradeData', data)
    }).catch(err => {
      Message.error(err.message)
    }).finally(() => {
      commit('setGradeDataLoading', false)
    })
  },
  queryClassData ({ commit }, params) {
    commit('setClassDataLoading', true)
    commit('setClassDataSearch', params)
    getClassData(params).then(data => {
      commit('setClassData', data)
    }).catch(err => {
      Message.error(err.message)
    }).finally(() => {
      commit('setClassDataLoading', false)
    })
  },
  queryClassRoster ({ commit }, params) {
    commit('setClassRosterLoading', true)
    commit('setClassRosterSearch', params)
    getClassRoster(params).then(data => {
      commit('setClassRoster', data)
    }).catch(err => {
      Message.error(err.message)
    }).finally(() => {
      commit('setClassRosterLoading', false)
    })
  },
  queryAllSchool ({ commit }, params) {
    queryAllSchool(params).then(data => {
      commit('setAllSchool', data)
    }).catch(err => {
      Message.error(err.message)
    })
  }
}
export default actions
