import { setToken, getToken, generateMenuByAuth } from '@/libs/util'
import { getRoleResources } from '@/api/resource'
// import { getResourcesByEmpNo } from '@/api/auth'
import Cookies from 'js-cookie'
import config from '@/config'
import { getResourcesByEmpNo } from '../../api/resource'
import { getUserResourceUrlList } from '../../api/auth'

export default {
  state: {
    userInfo: {},
    token: getToken(),
    access: [],
    hasGetInfo: false,
    allowMenuList: [], // 菜单列表
    allowButtonList: [] // 按钮权限列表
  },
  mutations: {
    setAccess (state, access) {
      state.access = access
    },
    setToken (state, token) {
      state.token = token
      setToken(token)
    },
    setUserInfo (state, uInfo) {
      state.userInfo = uInfo
    },
    setAllowMenuList (state, allowMenuList) {
      const res = generateMenuByAuth(allowMenuList)
      state.allowMenuList = res
    },
    setAllowButtonList (state, allowButtonList) {
      state.allowButtonList = allowButtonList
    }
  },
  getters: {
    // messageUnreadCount: state => state.messageUnreadList.length,
    // messageReadedCount: state => state.messageReadedList.length,
    // messageTrashCount: state => state.messageTrashList.length,
    userInfo: state => state.userInfo,
    userAccess: state => state.access
  },
  actions: {
    getUserAccess ({ state, commit }) {
      console.time('auth')
      return new Promise((resolve, reject) => {
        let req = {
          systemId: 'splat',
          empNo: state.userInfo.id
          // type: 0
        }
        getRoleResources(req).then(res => {
          let access = []
          if (res.resourceList) {
            res.resourceList.forEach(obj => {
              access.push(obj.linkUrl)
            })
            access.push('efficiency')
            access.push('efficiencyMate')
          }
          commit('setAccess', access)
          resolve(access)
        }).catch(err => {
          commit('setAccess', config.whiteList)
          reject(err)
        })
      })
    },
    // 登录
    // handleLogin ({ commit }, { userName, password }) {
    //   userName = userName.trim()
    //   return new Promise((resolve, reject) => {
    //     login({
    //       userName,
    //       password
    //     }).then(res => {
    //       const data = res.data
    //       commit('setToken', data.token)
    //       resolve()
    //     }).catch(err => {
    //       reject(err)
    //     })
    //   })
    // },
    // 退出登录
    handleLogOut ({ state, commit }) {
      return new Promise((resolve, reject) => {
        // logout(state.token).then(() => {
        //   commit('setToken', '')
        //   commit('setAccess', [])
        //   resolve()
        // }).catch(err => {
        //   reject(err)
        // })
        // 如果你的退出登录无需请求接口，则可以直接使用下面三行代码而无需使用logout调用接口
        commit('setToken', '')
        commit('setAccess', [])
        resolve()
      })
    },
    // 获取 一站式平台登录人信息
    getLoginInfo ({ state, commit }) {
      let columbusUserInfo = Cookies.get('columbusUserInfo')
      columbusUserInfo = JSON.parse(decodeURIComponent(columbusUserInfo))
      commit('setUserInfo', columbusUserInfo)
    },
    // 根据用户信息 获取左侧菜单
    queryUserAllowMenuList ({ rootState, state, commit }) {
      // console.log(rootState.roleRelativeData)
      let st = setInterval(() => {
        const ROLEID = rootState.roleRelativeData.userInfo.authRoleId
        if (ROLEID) {
          clearInterval(st)
          return new Promise((resolve, reject) => {
            getResourcesByEmpNo({ type: 0, roleId: ROLEID }).then(resp => {
              if (resp) {
                const resourceList = resp.children
                commit('setAllowMenuList', resourceList || [])
                resolve()
              } else {
                commit('setAllowMenuList', [])
              }
            }).catch(err => {
              reject(err)
            })
          })
        }
      }, 100)
    },
    // 获取用户 按钮权限列表
    queryUserAllowButtonList ({ rootState, state, commit }) {
      // console.log(rootState.roleRelativeData)
      return new Promise((resolve, reject) => {
        getUserResourceUrlList({ type: 2 }).then(resp => {
          if (resp) {
            commit('setAllowButtonList', resp || [])
            resolve()
          } else {
            commit('setAllowButtonList', [])
          }
        }).catch(err => {
          reject(err)
        })
      })
    }
  }
}
