
import { getUnAppointmentCount } from '@/api/appointmentApply'
import Cookie from 'js-cookie'
import { mapState } from 'vuex'
// 侧边栏待处理任务提醒
export default {
  computed: {
    ...mapState({
      crmRoleId: state => state.roleRelativeData.userInfo.crmRoleId,
      cityCode: state => state.roleRelativeData.userInfo.cityCode,
      crmDeptId: state => state.roleRelativeData.userInfo.crmDeptId
    })
  },
  methods: {
    // 任务推送
    async pushTask (task) {
      try {
        if (!task || !task.type) return
        let num = 0
        // console.log('====', this.crmRoleId, this.cityCode, this.crmDeptId)
        switch (task.type) {
          case 'unAppointmentCountChange':
            if ((this.crmRoleId === 2 || this.crmRoleId === 4 || this.crmRoleId === 5) && this.cityCode) {
              const params = {
                cityCode: this.crmRoleId === 2 ? this.cityCode : null,
                empNo:
                  this.crmRoleId === 5
                    ? JSON.parse(Cookie.get('columbusUserInfo')).id
                    : null,
                deptId: this.crmRoleId === 4 ? this.crmDeptId : null
              }
              num = await getUnAppointmentCount(params)
            }
            break
          default: break
        }
        const routerNameArr = task.routeName.split('-')
        const obj = this.makeUpObj(routerNameArr, num)
        const sideMenuTasks = Object.assign({}, this.$store.state.app.sideMenuTasks, obj)
        this.$store.commit('setSideMenuTasks', sideMenuTasks)
      } catch (error) {
        this.$Message.error({ content: `${error.message}`, duration: 5 })
      }
    },

    makeUpObj (attrs, val) {
      const obj = {}
      let oddObj = {} // 用于复制奇数项obj
      let evenObj = {} // 用于复制偶数项obj
      if (attrs.length === 1) {
        obj[attrs[0]] = val
      }
      for (let i = 0; i < attrs.length; i++) {
        if (i === attrs.length - 1 && i !== 0) {
          if (i % 2 === 0) {
            oddObj[attrs[i]] = val
          } else {
            evenObj[attrs[i]] = val
          }
          break
        }
        if (i === 0) {
          evenObj = obj[attrs[i]] = {}
        }
        if (i % 2 === 1) {
          oddObj = evenObj[attrs[i]] = {}
        }
        if (i % 2 === 0 && i !== 0) {
          evenObj = oddObj[attrs[i]] = {}
        }
      }
      return obj
    }
  }

}
