<template>
    <div class="wrapper">
      <div class="title">
        <span>请输入要发送的班级ID</span>
        <el-icon class="el-icon-close" @click.native="close"></el-icon>
      </div>
      <div class="form">
        <el-input v-model="classId" style="width: 300px" size="small" placeholder="请从班级详情复制该班级的班级ID"></el-input>
        <el-button size="small" :loading="searching" @click="query" :disabled="!classId">查询</el-button>
      </div>
      <div v-if="classInfo">
        <span style="margin-right: 10px">{{classInfo.name}}</span>
        <el-button size="small" :loading="sending" @click="send">发送</el-button>
      </div>
    </div>
</template>

<script>
import { queryClassDetail } from '@/api/clazz'
export default {
  name: 'ClassInfo',
  data () {
    return {
      searching: false,
      sending: false,
      classId: '',
      classInfo: null,
      tagMap: {
        0: '线下小班',
        1: '线下',
        2: '线上小班',
        3: '线上'
      }
    }
  },
  methods: {
    close () {
      this.$store.commit('imchat/setClassInfoShow', false)
    },
    query () {
      this.searching = true
      queryClassDetail({
        classId: this.classId
      }).then(data => {
        if (data) {
          this.classInfo = data
        } else {
          this.$message.error('无此班级，请输入正确的班级ID')
        }
      }).catch(err => {
        this.$message.error('查询班级错误：' + err.message)
      }).finally(() => {
        this.searching = false
      })
    },
    send () {
      let classDate = ''
      let location = ''
      if (this.classInfo.startDate && this.classInfo.endDate) {
        const start = this.classInfo.startDate.split('-')
        const end = this.classInfo.endDate.split('-')
        classDate = `${start[1]}月${start[2]}日-${end[1]}月${end[2]}日`
      }
      if (this.classInfo.classtimeNames) {
        classDate = `${classDate} · ${this.classInfo.classtimeNames}`
      }
      if (this.classInfo.venueName) {
        location = this.classInfo.areaName + this.classInfo.venueName
      }
      const classInfo = {
        classId: this.classInfo.id,
        className: this.classInfo.name,
        teacher: this.classInfo.teacherNames,
        tutor: this.classInfo.tutorRealName,
        classDate,
        location,
        claBizType: this.classInfo.bizType,
        classType: this.classInfo.classType,
        tagId: this.classInfo.newClassType,
        tagName: this.tagMap[this.classInfo.newClassType],
        price: this.classInfo.price,
        courseId: this.classInfo.courseId
      }
      console.log(JSON.stringify(classInfo, null, 2))
      this.$store.dispatch('imchat/sendCustomMsg', {
        to: this.$store.state.imchat.currSessionId.replace(/^p2p-/, ''),
        content: {
          type: 40,
          data: classInfo
        },
        pushContent: `「${classInfo.className}」`
      })
    }
  }
}
</script>

