  .wrapper {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background: #f4f4f4;
    border: 1px solid #ccc;
    border-radius: 4px;
    min-height: 50px;
    width: 450px;
    padding: 12px 19px;
    box-sizing: border-box;
    .title {
      display: flex;
      justify-content: space-between;
      font-size: 16px;
      font-weight: bold;
      margin-bottom: 10px;
    }
    .form {
      margin-bottom: 10px;
    }
  }
</style>
