<template>
  <ul
    v-if="isSession"
    id="chat-list"
    ref="chatlist">
    <li
      v-if="canLoadMore"
      class="u-msg-more">
      <a
        href="javascript:;"
        @click="loadMore">查看更多消息</a>
    </li>
    <li
      v-else
      class="u-msg item-time">
      ---已无更多记录---
    </li>
    <chat-item
      v-for="(msg, index) in msglist"
      :raw-msg="msg"
      :key="msg.idClient + index"
      @msg-loaded="msgLoaded"
      @editInfo="$emit('editInfo')"
    />
  </ul>
</template>

<script>
import ChatItem from './ChatItem'
export default {
  components: {
    ChatItem
  },
  props: {
    canLoadMore: {
      type: [String, Boolean],
      default: true
    },
    isSession: {
      type: Boolean,
      default: false
    },
    msglist: {
      type: Array,
      default () {
        return []
      }
    },
    scrollDown: {
      required: true,
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      msgLoadedTimer: null
    }
  },
  methods: {
    msgLoaded () {
      clearTimeout(this.msgLoadedTimer)
      this.msgLoadedTimer = setTimeout(() => {
        this.$emit('msgs-loaded')
      }, 50)
    },
    loadMore () {
      let chatList = this.$refs.chatlist
      let chatListScroll = chatList.scrollTop
      if (chatListScroll <= 5) {
        this.$emit('update:scrollDown', false)
        this.$emit('load-more')
      }
    }
  }
}
</script>
<style lang="less" scoped>
li {
  list-style: none;
  text-align: center;
}
.u-msg-more {
  padding-top: 10px;
  text-align: center;
}
</style>
