<template>
  <div class="side-menu-wrapper">
    <slot></slot>
    <Menu ref="menu" v-show="!collapsed" :active-name="activeName" :open-names="openedNames" :accordion="accordion" :theme="theme" width="auto" @on-select="handleSelect">
      <template v-for="item in menuList">
        <template v-if="item.children && item.children.length === 1">
          <side-menu-item v-if="showChildren(item)" :key="`menu-${item.name}`" :parent-item="item"></side-menu-item>
          <menu-item v-else :name="getNameOrHref(item, true)" :key="`menu-${item.children[0].name}`"><common-icon :type="item.children[0].icon || ''"/><span>{{ showTitle(item.children[0]) }}</span></menu-item>
        </template>
        <template v-else>
          <side-menu-item v-if="showChildren(item)" :key="`menu-${item.name}`" :parent-item="item"></side-menu-item>
          <menu-item v-else :name="getNameOrHref(item)" :key="`menu-${item.name}`"><common-icon :type="item.icon || ''"/><span>{{ showTitle(item) }}</span></menu-item>
        </template>
      </template>
    </Menu>
    <div class="menu-collapsed" v-show="collapsed" :list="menuList">
      <template v-for="item in menuList">
        <collapsed-menu v-if="item.children && item.children.length > 1" @on-click="handleSelect" hide-title :root-icon-size="rootIconSize" :icon-size="iconSize" :theme="theme" :parent-item="item" :key="`drop-menu-${item.name}`"></collapsed-menu>
        <Tooltip transfer v-else :content="showTitle(item.children && item.children[0] ? item.children[0] : item)" placement="right" :key="`drop-menu-${item.name}`">
          <a @click="handleSelect(getNameOrHref(item, true))" class="drop-menu-a" :style="{textAlign: 'center'}"><common-icon :size="rootIconSize" :color="textColor" :type="item.icon || (item.children && item.children[0].icon)"/></a>
        </Tooltip>
      </template>
    </div>
  </div>
</template>
<script>
import SideMenuItem from './side-menu-item.vue'
import CollapsedMenu from './collapsed-menu.vue'
import localStorage from '@/common/utils/localstorage'
import { getUnion } from '@/libs/util'
import {
  newTaskRemind
} from '@/api/crm'
import { getOriginClueCount } from '@/api/clue'
import mixin from './mixin'
import sideMenuTasksMixin from '../sideMenuTasksMixin'
export default {
  name: 'SideMenu',
  mixins: [ mixin, sideMenuTasksMixin ],
  components: {
    SideMenuItem,
    CollapsedMenu
  },
  props: {
    menuList: {
      type: Array,
      default () {
        return []
      }
    },
    collapsed: {
      type: Boolean
    },
    theme: {
      type: String,
      default: 'light'
    },
    rootIconSize: {
      type: Number,
      default: 20
    },
    iconSize: {
      type: Number,
      default: 16
    },
    accordion: Boolean,
    activeName: {
      type: String,
      default: ''
    },
    openNames: {
      type: Array,
      default: () => []
    },
    userInfo: {
      type: Object
    }
  },
  data () {
    return {
      openedNames: [],
      st: '',
      checkSt: ''
    }
  },
  methods: {
    // 获取线索池新增订单数量
    async getClueTaskNum () {
      try {
        const num = await getOriginClueCount({ cityCode: '010', identityId: this.userInfo.identityId })
        const sideMenuTasks = Object.assign({}, this.$store.state.app.sideMenuTasks, {
          ClueMgt: {
            AdviserPool: num || 0
          }
        })
        this.$store.commit('setSideMenuTasks', sideMenuTasks)
      } catch (error) {
        this.$Message.error({
          content: '获取线索池未注册任务提醒数量失败' + error.message
        })
      }
    },
    // 获取任务工作台新增待处理任务数量
    getNewNum () {
      let lastVisitedTime = {}
      if (localStorage.get('lastVisitedTime')) {
        // window.localStorage.removeItem('lastVisitedTime')
        lastVisitedTime = localStorage.get('lastVisitedTime')
      } else {
        let timestamp = Date.now()
        timestamp = parseInt(timestamp / 1000)
        lastVisitedTime = {
          mytaskNewstudent: timestamp,
          mytaskConsultstask: timestamp
        }
        localStorage.set('lastVisitedTime', lastVisitedTime)
      }
      newTaskRemind({
        assignAfter: lastVisitedTime.mytaskConsultstask
      }).then(res => {
        const sideMenuTasks = Object.assign({}, this.$store.state.app.sideMenuTasks, {
          resource: {
            advisorWorkbench: res.newAdvisorTaskCount || 0
          }
        })

        this.$store.commit('setSideMenuTasks', sideMenuTasks)
      }).catch(err => {
        this.$Message.error({
          content: '获取任务数量失败：' + err.message
        })
      })
    },
    handleSelect (name) {
      this.$emit('on-select', name)
    },
    getOpenedNamesByActiveName (name) {
      return this.$route.matched.map(item => item.name).filter(item => item !== name)
    },
    updateOpenName (name) {
      if (name === this.$config.homeName) this.openedNames = []
      else this.openedNames = this.getOpenedNamesByActiveName(name)
    },
    checkTaskCount () {
      this.checkSt = setInterval(() => {
        if (this.userInfo && this.userInfo.crmRoleId) {
          clearInterval(this.checkSt)
          this.getTaskCount()
        }
      }, 2000)
    },
    getTaskCount () {
      if (this.st) clearInterval(this.st)
      this.st = setInterval(() => {
        if (this.userInfo.crmRoleId === 5 || this.userInfo.crmRoleId === 7) {
          this.getNewNum()
        }
        this.getClueTaskNum()
      }, 10000)
    }
  },
  computed: {
    textColor () {
      return this.theme === 'dark' ? '#fff' : '#495060'
    }
  },
  watch: {
    activeName (name) {
      if (this.accordion) this.openedNames = this.getOpenedNamesByActiveName(name)
      else this.openedNames = getUnion(this.openedNames, this.getOpenedNamesByActiveName(name))
    },
    openNames (newNames) {
      this.openedNames = newNames
    },
    openedNames () {
      this.$nextTick(() => {
        this.$refs.menu.updateOpened()
      })
    },
    userInfo () {
      // 侧边栏待处理任务提醒
      this.pushTask({ type: 'unAppointmentCountChange', routeName: 'operative-auditionMgt' })
    }
  },
  mounted () {
    this.openedNames = getUnion(this.openedNames, this.getOpenedNamesByActiveName(name))
    this.checkTaskCount()
  },
  beforeDestroy () {
    clearInterval(this.st)
  }
}
</script>
<style lang="less">
@import './side-menu.less';
</style>
