<template>
  <div>
    <section id="im-shadow" @click="clickShadow" v-show="!isMini"></section>
    <el-popover
      :visible-arrow="false"
      v-model="visible"
      trigger="manual"
      placement="top"
      :width="crmRoleId ? 900 : 600"
      popper-class="poperClass"
      @show="popoverShow"
      @hide="popoverHide">
      <div
        ref="chatBox"
        class="im-wrap im-wrap-oc" :class="{'im-wrap-oc': crmRoleId === 8}">
        <Session ref='imSession' @editInfo="editInfo"/>
        <div class="im-content im-oc-content">
          <Chat @endTheService='endTheServiceHandle' @editInfo="editInfo"/>
        </div>
        <div class="im-chat-order" v-if="currSessionId">
          <header>历史工单</header>
          <section>
            <TalOnlineAdviserRecord ref="adviserRecord" :cityCode="currImTitleInfo.areaCode" :uid="currImTitleInfo.pyUid"></TalOnlineAdviserRecord>
          </section>
          <section v-if='currImTitleInfo.id && (currImTitleInfo.status !== 2) &&  userInfos[currImTitleInfo.nimAccid].consultLabel !== 1'>
            <ChatOrder
              :showUserInfo='false'
              :sessionUserInfo='currImTitleInfo'
              @refresh='refreshHandle'></ChatOrder>
          </section>
        </div>
        <ClassInfo v-if="classInfoShow"></ClassInfo>
        <MsgRecord v-if="msgRecordShow"></MsgRecord>
        <imLoading></imLoading>
      </div>
      <div
        slot="reference"
        :style="{visibility:isMini?'visible':'hidden'}"
        class="im-mini"
        @click.stop="onClickMiniBox">
        <div class="im-status">
          <i :class="['glyphicon', 'glyphicon-envelope', {'flash': remind}]"/>
          <span v-if="status && status !== 'sucess'">
            {{ status === 'kicked' ? 'im被踢，暂不能使用' : status }}
          </span>
          <span
            v-else
            :class="unread ? 'im-status-unread' : ''">
            {{ unread ? `您有 ${unread} 条未读消息` : crmRoleId === 8 ? '在线顾问' : `家庭顾问` }}
          </span>
          <!--重连按钮-->
          <el-button
            v-if="status === 'kicked'"
            type="text"
            @click.stop="reconnectEv">重连</el-button>
        </div>

      </div>
    </el-popover>
    <el-dialog
      v-if='dialogVisible'
      title="学员工单"
      :visible.sync="dialogVisible"
      width="200">
      <ChatOrder
        :inTheEnd='true'
        :sessionUserInfo='currImTitleInfo'
        @close='closeOrderHandle'></ChatOrder>
      <ChatTitle
        @goCustomerDetail="goCustomerDetail"></ChatTitle>
    </el-dialog>
    <el-dialog
      v-if='userInfoModal'
      :visible.sync="userInfoModal"
      title="修改资料"
      width="400px">
      <UserInfo @success="userInfoModal = false"></UserInfo>
    </el-dialog>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import Session from './Session.vue'
import Chat from './Chat.vue'
import ChatOrder from './ChatOrder'
import ChatTitle from './ChatTitle'
import UserInfo from './UserInfo'
import ClassInfo from './ClassInfo'
import MsgRecord from './MsgRecord'
import imLogger from '@/common/logger/imLogger.js'
import util from '@/common/util'
import imLoading from './Loading'
import {
  browser
} from '../../utils/utils'
import CustomerMixin from '../mixin/customerDetail'
import { getAdvisorAuthorizeByCurrentTime } from '@/api/advisor'
const msgAudio = require('../../../public/msg.mp3')
export default {
  mixins: [ CustomerMixin ],
  components: {
    Session,
    Chat,
    ChatOrder,
    ChatTitle,
    imLoading,
    UserInfo,
    ClassInfo,
    MsgRecord
  },
  data () {
    return {
      isMini: true,
      clickInIm: false,
      audioCtx: {},
      visible: false,
      heartbeatInterval: '',
      dialogVisible: false,
      sessionUserInfo2: {},
      userInfoModal: false
    }
  },
  computed: {
    remind () {
      // let msgs = this.$store.state.imchat.msgs
      let unreadList = this.$store.state.imchat.currShowSessionList.filter(item => {
        if (item.scene === 'p2p' || /^p2p-/.test(item.id)) {
          let to = item.to || item.id.replace(/^p2p-/, '')
          if (to === this.myUserId) return false
        }
        // const sessionMsgList = msgs[item.id] || []
        // const lastMsg =
        //   sessionMsgList[sessionMsgList.length - 1] || item.lastMsg || {}
        // if (item.unread > 0 || lastMsg.flow === 'in') {
        if (item.unread > 0) {
          return true
        } else {
          return false
        }
      })
      return unreadList.length > 0
    },
    unread () {
      let unreadCount = 0
      this.$store.state.imchat.currShowSessionList.forEach(item => {
        unreadCount += item.unread || 0
      })
      return unreadCount
    },
    ...mapState({
      crmRoleId: state => state.roleRelativeData.userInfo.crmRoleId,
      // unread: state => state.imchat.unread,
      isKicked: state => state.imchat.isKicked,
      status: state => state.imchat.status,
      sessions: state => state.imchat.sessions,
      isFullscreenImgShow: state => state.imchat.isFullscreenImgShow,
      isIMConnecting: state => state.imchat.isIMConnecting,
      myUserId: state => `p2p-${state.imchat.userUID}`,
      isShowMini: state => state.imchat.isShowMini,
      currSessionId: state => state.imchat.currSessionId,
      currImTitleInfo: state => state.imchat.currImTitleInfo,
      userInfos: state => state.imchat.userInfos,
      classInfoShow: state => state.imchat.classInfoShow,
      msgRecordShow: state => state.imchat.msgRecordShow
    }),
    userInfo () {
      let userInfo = this.userInfos[this.to]
      let { stu_loginname, stu_mobile } = userInfo
      return { stu_loginname, stu_mobile }
    }
  },
  watch: {
    isShowMini (newVal, oldVal) {
      if (!newVal) {
        this.visible = !newVal
      }
    },
    isMini (newVal, oldVal) {
      // this.visible = !newVal
    },
    unread (newUnread, oldUnread) {
      if (newUnread > 0) {
        if (browser().ie) { // ie下播放音频
          let audio = document.getElementById('msg')
          audio.play()
        } else {
          var oscillator1 = this.audioCtx.createOscillator()
          var gainNode1 = this.audioCtx.createGain()
          oscillator1.connect(gainNode1)
          gainNode1.connect(this.audioCtx.destination)
          oscillator1.type = 'sine'
          oscillator1.frequency.value = 1000
          gainNode1.gain.setValueAtTime(0, this.audioCtx.currentTime)
          gainNode1.gain.linearRampToValueAtTime(
            1,
            this.audioCtx.currentTime + 0.01
          )
          oscillator1.start(this.audioCtx.currentTime)
          oscillator1.stop(this.audioCtx.currentTime + 0.3)

          var oscillator2 = this.audioCtx.createOscillator()
          var gainNode2 = this.audioCtx.createGain()
          oscillator2.connect(gainNode2)
          gainNode2.connect(this.audioCtx.destination)
          oscillator2.type = 'sine'
          oscillator2.frequency.value = 1500
          gainNode2.gain.setValueAtTime(1, this.audioCtx.currentTime + 0.3)
          oscillator2.start(this.audioCtx.currentTime + 0.3)
          oscillator2.stop(this.audioCtx.currentTime + 0.6)

          var oscillator3 = this.audioCtx.createOscillator()
          var gainNode3 = this.audioCtx.createGain()
          oscillator3.connect(gainNode3)
          gainNode3.connect(this.audioCtx.destination)
          oscillator3.type = 'sine'
          oscillator3.frequency.value = 2000
          gainNode3.gain.setValueAtTime(1, this.audioCtx.currentTime + 0.6)
          oscillator3.start(this.audioCtx.currentTime + 0.6)
          gainNode3.gain.exponentialRampToValueAtTime(
            0.001,
            this.audioCtx.currentTime + 0.9
          )
          oscillator3.stop(this.audioCtx.currentTime + 0.9)
        }
        // 声音提醒日志
        const roleInfo = this.$store.state.roleRelativeData.roleInfo
        imLogger({
          action: 'im-audio-remind',
          from: 'PC',
          teacher_id: roleInfo.nimAccid,
          teacher_name: roleInfo.userName,
          teacher_workcode: roleInfo.userWorkCode,
          act_time_connect: new Date().getTime()
        })
      }
    }
  },
  created () {
    // document.addEventListener(
    //   'click',
    //   e => {
    //     console.log(this.$refs.chatBox)
    //     console.log(this.$refs.chatBox.contains(e.target))
    //     // 点击图片放大IM聊天窗口不消失
    //     let viewContainer = document.querySelector('.viewer-container')
    //     if (!this.$refs.chatBox.contains(e.target)) {
    //       if (viewContainer && viewContainer.contains(e.target)) {
    //         return
    //       }
    //       this.visible = false
    //       this.$store.dispatch('imchat/resetCurrSession')
    //     }
    //   },
    //   false
    // )
  },
  mounted () {
    if (!browser().ie) {
      window.AudioContext = window.AudioContext || window.webkitAudioContext
      this.audioCtx = new AudioContext()
    } else { // ie浏览器下使用audio播放信息提示音
      var audio = document.createElement('audio')
      audio.src = msgAudio
      audio.id = 'msg'
      document.body.appendChild(audio)
    }
    // ***********************切换tab后关闭IM，切换回来重启*************************
    // $(document)
    //   .off('visibilitychange.im')
    //   .on('visibilitychange.im', e => {
    //     if (document.visibilityState === 'visible') {
    //       this.$store.dispatch('connect')
    //     } else {
    //       this.isMini = true
    //       if (this.$store.state.nim) {
    //         this.$store.state.nim.disconnect()
    //       }
    //       this.$store.commit('updateKicked', false)
    //       this.$store.commit('updateIMStatus', {
    //         status: '正在连接...'
    //       })
    //       this.$store.dispatch('resetCurrSession')
    //     }
    //   })
    // $(document)
    //   .on('click.im', () => {
    //     if (!this.clickInIm) {
    //       this.isMini = true
    //       this.$store.dispatch('resetCurrSession')
    //     } else {
    //       this.clickInIm = false
    //     }
    //     if (this.isIMConnecting) {
    //       return
    //     }
    //     if (this.$store.state.isKicked) {
    //       this.$store.commit('updateIsIMConnecting', true)
    //       this.$store.dispatch('connect')
    //     }
    //   })
    //   .on('startSession', (e, data) => {
    //     if (!data.accid) {
    //       return
    //     }
    //     if (this.$store.state.status) {
    //       return this.$message('IM正在连接，请稍后...')
    //     }
    //     let user = this.$store.state.user
    //     report({
    //       action: 'click_but',
    //       action_effect_obj_value: 'but_resource_send_msg_from_pc',
    //       uid: data.accid,
    //       school: user.city_name,
    //       teacher_id: user.user_id,
    //       act_time_tch: window.moment().format('YYYY-MM-DD HH:mm:ss'),
    //       status: 'success',
    //       tch_name: `家庭顾问：${user.username}`,
    //       stu_name: data.stuName
    //     })
    //     this.isMini = false
    //     this.$store.dispatch('setCurrSession', `p2p-${data.accid}`)
    //     this.$store.dispatch('getStuPhoneAndNum', data.accid)
    //   })

    // await this.insertEditorTag()
    // // debugger
    // this.$store.commit('imchat/setNimSDK', NIM)
    //
    // // 请求顾问IM 账户 和 token，顾问当前绑定的学员
    // await Promise.all([
    //   this.$store.dispatch('roleRelativeData/gainRoleInfo'),
    //   this.$store.dispatch('imchat/getServerSessions')
    // ])
    // this.$store.dispatch('imchat/connect')
    // this.heartbeat()
    // this.heartFun()
    this.init()
  },
  beforeDestroy () {
    clearInterval(this.heartbeatInterval)
    clearInterval(this.getAgentInterval)
    // $(document).off('click.im')
    // $(document).off('startSession')
  },
  methods: {
    async insertTrack () {
      return new Promise(resolve => {
        let formerTag = document.getElementById('xesTrack')
        if (formerTag) {
          formerTag.remove()
        }

        let editorTag = document.createElement('script')
        editorTag.type = 'text/javascript'
        editorTag.src = '//static-xesapi.speiyou.cn/xesFeProject/xesFeSource/xesJsLibrary/talSensors.js'
        editorTag.id = 'xesTrack'
        let head = document.getElementsByTagName('head')[0]
        head.appendChild(editorTag)
        // add listener
        editorTag.addEventListener('load', () => {
          resolve()
        })
      })
    },
    async init () {
      await this.insertEditorTag()
      await this.insertWebRTC()
      // await this.insertTrack() //在index.html引入
      SDK.NIM.use(WebRTC)
      this.$store.commit('imchat/setNimSDK', SDK.NIM)
      // this.$store.commit('imchat/setNimWebRTC', WebRTC)
      // 请求顾问IM 账户 和 token，顾问当前绑定的学员
      await Promise.all([
        this.$store.dispatch('roleRelativeData/gainRoleInfo')
        // this.$store.dispatch('imchat/getServerSessions')
      ])
      this.$store.dispatch('imchat/connect')

      this.$store.dispatch('imchat/initNimWebRTC')
      this.heartbeat()
      this.heartFun()
      // this.getAgent()
      // this.getAgentInterval = setInterval(() => {
      //   this.getAgent()
      // }, 60 * 1000)
    },
    getAgent () {
      getAdvisorAuthorizeByCurrentTime().then(data => {
        this.$store.commit('imchat/setAgent', data)
      }).catch(err => {
        this.$Message.error('查询当前是否代班状态错误：' + err.message)
      })
    },
    popoverShow () {
      this.isMini = false
    },
    popoverHide () {
      this.isMini = true
      this.$store.commit('imchat/setShowMini', true)
      // this.$store.commit('imchat/currImTitleInfo', {})
    },
    insertEditorTag () {
      return new Promise(resolve => {
        // clear former tag
        let formerTag = document.getElementById('imWeb')
        if (formerTag) {
          formerTag.remove()
        }

        let editorTag = document.createElement('script')
        editorTag.type = 'text/javascript'
        // old-config changed by gm add section 2018-11-20
        // editorTag.src = '//wxcp-img.oss-cn-beijing.aliyuncs.com/fe/static/ueditor1_4_3_3/ueditor.all.js'
        editorTag.src =
          // '//xesapp-xesapi.oss-cn-beijing.aliyuncs.com/crm/fe/static/js/NIM_Web_SDK_v5.0.1.js'
          // 参考云信web开发集成／即成JS代码部分的文档
          // '//speiyou-tbe-fe.oss-cn-beijing.aliyuncs.com/NIM_Web_WebRTC_v7.3.0.js'
          '//speiyou-tbe-fe.oss-cn-beijing.aliyuncs.com/NIM_Web_SDK_v7.7.2.js'
        editorTag.id = 'imWeb'
        let head = document.getElementsByTagName('head')[0]
        head.appendChild(editorTag)
        // add listener
        editorTag.addEventListener('load', () => {
          resolve()
        })
      })
    },
    insertWebRTC () {
      return new Promise(resolve => {
        let formerTag = document.getElementById('imWebRTC')
        if (formerTag) {
          formerTag.remove()
        }

        let editorTag = document.createElement('script')
        editorTag.type = 'text/javascript'
        editorTag.src = '//speiyou-tbe-fe.oss-cn-beijing.aliyuncs.com/NIM_Web_WebRTC_v7.7.2.js'
        editorTag.id = 'imWebRTC'
        let head = document.getElementsByTagName('head')[0]
        head.appendChild(editorTag)
        // add listener
        editorTag.addEventListener('load', () => {
          resolve()
        })
      })
    },
    onClickMini () {
      if (this.$store.state.status) {
        return
      }
      this.isMini = false
      this.clickInIm = true
      // 选择第一个会话
      // let sessionList = this.$store.state.sessionlist;
      // if (sessionList.length > 0 && sessionList[0].id !== this.myUserId) {
      //     this.$store.dispatch('setCurrSession', sessionList[0].id);
      // } else if (sessionList.length > 1) {
      //     this.$store.dispatch('setCurrSession', sessionList[1].id);
      // }
    },
    onClickMiniBox () {
      if (this.status === 'kicked') {
        return
      }
      if (!window.pangu_drag) {
        this.visible = true
      } else {
        window.pangu_drag = false
      }
    },
    async reconnectEv () {
      await this.$store.dispatch('roleRelativeData/gainRoleInfo')
      this.$store.dispatch('imchat/connect')
    },
    heartbeat () {
      // const roleInfo = this.$store.state.roleRelativeData.roleInfo
      this.heartbeatInterval = setInterval(() => {
        this.heartFun()
      }, 300000)
    },
    heartFun () {
      const roleInfo = this.$store.state.roleRelativeData.roleInfo
      imLogger({
        action: 'heart-beat',
        from: 'PC',
        teacher_id: roleInfo.nimAccid,
        teacher_name: roleInfo.userName,
        teacher_workcode: roleInfo.userWorkCode,
        act_time_connect: new Date().getTime(),
        heartStatus: this.status,
        deviceInfo: util.deviceInfo(),
        deviceId: util.getuuid()
      })
    },
    clickShadow () {
      this.visible = false
      this.$store.dispatch('imchat/resetCurrSession')
    },
    endTheServiceHandle (sessionUserInfo) {
      this.dialogVisible = true
      this.sessionUserInfo = sessionUserInfo
    },
    sessionUserInfoHandle (info) {
      this.sessionUserInfo = info
    },
    closeOrderHandle () {
      this.$refs['imSession'].setCurrSession()
      this.dialogVisible = false
      this.refreshHandle()
    },
    refreshHandle () {
      this.$refs['adviserRecord'].refresh()
    },
    editInfo () {
      this.userInfoModal = true
    }
  }
}
</script>
<style>
