<style lang="less" scoped>

#im-shadow {
  position: absolute;
  width:100%;
  height: 100%;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: transparent;
  /*pointer-events: none;*/
  z-index: 1000;
}
.im-wrap {
  /*width: 500px;*/
  border: 1px solid #ccc;
  height: 400px;
  right: 30px;
  bottom: 20px;
  background: #fff;
  box-shadow: 0 0 10px #ccc;
  z-index: 2000;
  overflow: hidden;
  display: flex;
  .im-chat-order {
    display: none;
  }
  &.im-wrap-oc {
    height: 500px;
    .im-chat-order {
      display: flex;
      flex-direction: column;
      padding: 10px;
      font-size: 12px;
      width: 240px;
      &>section {
        flex: 1;
        overflow: auto;
      }
    }
  }
  .im-content {
    box-shadow: -3px 0 8px #ccc;
    font-size: 14px;
    &.im-oc-content {
      width: 450px;
    }
  }
}
.im-mini {
  position: fixed;
  width: 260px;
  border: 1px solid #ccc;
  line-height: 40px;
  height: 40px;
  right: 30px;
  bottom: 20px;
  background: #fff;
  box-shadow: 0 0 10px #ccc;
  z-index: 999;
  opacity: 0.8;
  &:hover {
    opacity: 1;
    cursor: pointer;
  }
  i {
    margin-right: 5px;
  }
}
.im-status {
  padding: 0 10px;
}
.im-status-unread {
  -webkit-animation: blink 1.5s infinite;
}

@-webkit-keyframes blink {
  0% {
    color: #2c3e50;
  }
  50% {
    color: #f00;
  }
  100% {
    color: #2c3e50;
  }
}
@keyframes twinkling {
  0%,
  50%,
  100% {
    opacity: 1;
  }
  25%,
  75% {
    opacity: 0;
  }
}
.flash {
  animation: twinkling 1s 0.2s ease both infinite;
}
</style>
