<template>
  <div v-show="!!currSessionId">
    <div class="im-chat-bar">
      <i
        class="im-icon im-emoji"
        title="表情"
        @click.stop="showEmoji"/>
      <span
        class="im-icon im-file im-img"
        title="文件"
      >
        <input
          ref="imageToSent"
          style="overflow: hidden"
          type="file"
          accept="image/png, image/gif, image/jpeg, image/bmp, image/jpg"
          @change="sendFileMsg('img')">
      </span>
      <i
        class="im-icon im-audio"
        title="语音"
        @click.stop="callAudio"/>
      <i
        class="im-icon el-icon-tickets"
        title="课程卡片"
        @click.stop="openClassInfo"/>
      <el-button size="mini" type="primary" plain class="record" @click="showRecord">历史沟通记录</el-button>
      <!--<span>-->
        <!--class="im-icon im-file img-files"-->
        <!--title="文件"-->
      <!--&gt;-->
        <!--<input-->
          <!--ref="fileToSent"-->
          <!--type="file"-->
          <!--accept="application/vnd.ms-excel, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, .pdf, application/vnd.openxmlformats-officedocument.wordprocessingml.document, application/msword"-->
          <!--@change="sendFileMsg('file')">-->
      <!--</span>-->
      <div
        v-show="isEmojiShown"
        class="im-emoji-list clearfix">
        <img
          v-for="(item,key) in emoji"
          :key="key"
          :src="item.img"
          @click.stop="selectEmoji(key)">
      </div>
      <el-button v-if='currImTitleInfo.id && (currImTitleInfo.status !== 2 && currImTitleInfo.status !== 4)' size="mini" class='end-service-btn' @click='endTheServiceHandle'>结束本次服务</el-button>
    </div>
    <div class="im-chat-input im-chat-input-oc clearfix">
      <div class="input">
        <textarea
          ref="textarea"
          :placeholder="canInput ? '在此处输入文字(shift+enter换行)' : '请选择聊天对象'"
          :id="'im-input-' + id"
          :disabled="!canInput"
          v-model="msgToSent"
          name=""
          cols="30"
          rows="10"
          @keyup.enter="sendMsgByEnter"
          @click='isEmojiShown = false'/>
      </div>
      <div>
        <button
          class="action"
          @click="sendMsg">发送</button>
      </div>

    </div>
  </div>
</template>

<script>
import emoji from '@/assets/js/emoji'
import util from '@/common/util'
import { mapState } from 'vuex'
export default {
  props: {
    canInput: {
      type: Boolean,
      default () {
        return false
      }
    },
    to: {
      type: String,
      default: '0'
    },
    scene: {
      type: String,
      default: 'p2p'
    }
  },
  data () {
    return {
      emoji: emoji.emojiList.emoji,
      isEmojiShown: false,
      msgToSent: '',
      id: +new Date() + Math.round(Math.random() * 10000)
    }
  },
  computed: {
    ...mapState({
      currImTitleInfo: state => state.imchat.currImTitleInfo,
      crmRoleId: state => state.roleRelativeData.userInfo.crmRoleId,
      currSessionId: state => state.imchat.currSessionId,
      classInfoShow: state => state.imchat.classInfoShow,
      msgRecordShow: state => state.imchat.msgRecordShow
    })
  },
  watch: {
    currSessionId (oldVal, newVal) {
      if (oldVal !== newVal) {
        this.$store.commit('imchat/setClassInfoShow', false)
        this.$store.commit('imchat/setMsgRecordShow', false)
        this.msgToSent = ''
      }
    }
  },
  updated () {
    window.document.body.addEventListener('click', (e) => {
      this.isEmojiShown = false
    })
  },
  methods: {
    endTheServiceHandle () {
      if (this.$store.state.imchat.calling ||
        this.$store.state.imchat.beCalling ||
        this.$store.state.imchat.callOther) {
        this.$Message.warning({
          content: '语音通话中不可结束工单',
          duration: 5
        })
        return
      }
      this.$emit('endTheService', true)
    },
    selectEmoji (emoji) {
      this.msgToSent += emoji
      this.hideEmoji()
      this.$refs.textarea.focus()
    },
    showEmoji () {
      this.isEmojiShown = true
    },
    hideEmoji () {
      this.isEmojiShown = false
    },
    sendMsgByEnter (event) {
      if (!(event.ctrlKey || event.shiftKey || event.altKey || event.metaKey)) {
        event.preventDefault()
        this.sendMsg()
      }
    },
    sendMsg () {
      this.msgToSent = this.msgToSent.trim()
      if (!this.msgToSent) return
      this.$store.dispatch('imchat/sendMsg', {
        scene: this.scene,
        to: this.to,
        text: this.msgToSent
      })
      this.msgToSent = ''
    },
    sendFileMsg (type) {
      let $img = this.$refs.imageToSent
      let $file = this.$refs.fileToSent
      if (type === 'img' && !/\.(png|jpg|bmp|jpeg|gif)$/i.test($img.value)) {
        this.$message('暂不支持发送该类型图片!')
      } else if (type === 'file' && !/\.(doc|docx|pdf|xls|xlsx)$/i.test($file.value)) {
        this.$message('暂不支持发送该类型文件!')
      } else {
        if ($img.value || $file.value) {
          this.$store.dispatch('imchat/sendFileMsg', {
            scene: this.scene,
            to: this.to,
            fileInput: type === 'img' ? $img : $file
          })
        }
      }
    },
    callAudio () {
      const browserName = util.browser()
      console.log(browserName)
      if (browserName.includes('Chrome')) {
        this.$store.dispatch('imchat/NimCall')
      } else {
        this.$Message.warning({
          content: '为保证语音功能正常使用，请使用谷歌浏览器',
          duration: 5
        })
      }
      // this.$store.dispatch('imchat/NimCall', 'chengji2')
    },
    openClassInfo () {
      this.$store.commit('imchat/setClassInfoShow', !this.classInfoShow)
    },
    showRecord () {
      this.$store.commit('imchat/setMsgRecordShow', !this.msgRecordShow)
    }
  }
}
</script>

