import util from '@/common/util'
// import cookie from '@/common/utils/cookie'
import cookie from 'js-cookie'
import { NIM } from '@/common/commonData'
import Vue from 'vue'

export default {
  setNimSDK (state, nimSDK) {
    state.nimSDK = nimSDK
  },
  setNimWebRTC (state, webRTC) {
    state.nimWebRTC = webRTC
  },
  setShowMini (state, val) {
    state.isShowMini = val
  },
  setCalling (state, val) {
    state.calling = val
  },
  setCallTimeWrapper (state, val) {
    state.callTimeWrapper = val
  },
  setBeCalling (state, val) {
    state.beCalling = val
  },
  setCallOther (state, val) {
    state.callOther = val
  },
  setCallerAccount (state, val) {
    state.callerAccount = val
  },
  updateLoading (state, status) {
    // clearTimeout(state.loadingTimer)
    // state.loadingTimer = setTimeout(() => {
    //   state.isLoading = status
    //   Vue.prototype.$store.commit('updateIMStatus', { status: '正在连接...' })
    // }, 20)
    state.isLoading = status
  },
  updateProcess (state, data) {
    state.updateProcess = data
  },
  updateSessionFilter (state, list) {
    state.sessionFilter = list
  },
  updateSessions (state, obj) {
    let { sessions, isReplace } = obj
    const nim = NIM
    if (isReplace) {
      state.sessionlist = sessions
    } else {
      state.sessionlist = nim.mergeSessions(state.sessionlist, sessions)
    }
    state.sessionlist.sort((a, b) => {
      return b.updateTime - a.updateTime
    })
    state.sessionlist.forEach(item => {
      state.sessionMap[item.id] = item
    })
    Vue.prototype.$store.dispatch('imchat/updateMsgsUnread', state.sessionlist)
  },
  deleteSessions (state, sessionIds) {
    const nim = NIM
    state.sessionlist = nim.cutSessionsByIds(state.sessionlist, sessionIds)
  },
  // 初始化时保留星标用户
  updateMuteUserInfo (state, users) {
    let userInfos = state.userInfos
    users.forEach(user => {
      let account = user.netEaseId || user.account || user.to
      if (account) {
        userInfos[account] = util.mergeObject(userInfos[account], user)
      }
    })
    state.userInfos = util.mergeObject(state.userInfos, userInfos)
  },
  // 初始化时合并用户信息并筛选出真实绑定用户
  updateUserInfo (state, users) {
    // console.log('存储的user', users)
    let userInfos = state.userInfos
    let store = Vue.prototype.$store
    // console.log(userInfos)
    // console.log('存储的userInfos', userInfos)
    users.forEach(user => {
      let account = user.netEaseId || user.account || user.to
      if (account) {
        userInfos[account] = util.mergeObject(userInfos[account], user)
      }
    })
    // console.log('存储的userInfos2222: ', userInfos)
    Object.keys(userInfos).forEach(item => {
      !userInfos[item].pyUid && delete userInfos[item]
    })
    const _userInfos = util.mergeObject(state.userInfos, userInfos)
    if (store.state.roleRelativeData.userInfo.crmRoleId) {
    // if (store.state.roleRelativeData.userInfo.crmRoleId === 8) { // TODO 0424
      // console.log(Object.keys(state.userInfos))
      let unBind = {}
      let bind = {}
      Object.keys(_userInfos).forEach(obj => {
        // console.log(obj, _userInfos[obj].status)
        if (_userInfos[obj].status === 1) {
          bind[obj] = _userInfos[obj]
        } else {
          unBind[obj] = _userInfos[obj]
        }
      })
      const _merge = Object.assign({}, bind, unBind)
      // debugger
      state.userInfos = _merge
    } else {
      state.userInfos = _userInfos
    }
  },
  updateKicked (state, isKicked) {
    let store = Vue.prototype.$store
    if (isKicked && !state.isKicked) {
      state.isKicked = isKicked
      store.commit('imchat/updateIMStatus', {
        status: 'kicked'
      })

      Vue.prototype
        .$confirm(
          '其他设备正在尝试登录，已自动退出IM，是否尝试重连？',
          '提示',
          {
            confirmButtonText: '重连',
            cancelButtonText: '取消',
            type: 'warning'
          }
        )
        .then(async () => {
          await store.dispatch('roleRelativeData/gainRoleInfo')
          store.dispatch('imchat/connect')
        })
        .catch(() => {})
    } else {
      state.isKicked = false
    }
  },
  updateIsIMConnecting (state, isIMConnecting) {
    state.isIMConnecting = isIMConnecting
  },
  updateUserUID (state, loginInfo) {
    state.userUID = loginInfo.accid
    cookie.set('accid', loginInfo.accid, {
      path: '/',
      expires: 1
    })
    cookie.set('token', loginInfo.token, {
      path: '/',
      expires: 1
    })
  },
  updateCurrSessionId (state, obj) {
    let type = obj.type || ''
    if (type === 'destroy') {
      state.currSessionId = null
    } else if (type === 'init') {
      if (obj.sessionId && obj.sessionId !== state.currSessionId) {
        state.currSessionId = obj.sessionId
      }
    }
  },
  // 追加一条消息
  putMsg (state, msg) {
    let sessionId = msg.sessionId
    if (!state.msgs[sessionId]) {
      state.msgs[sessionId] = []
    }
    Vue.prototype.$store.commit('imchat/updateMsgByIdClient', msg)
    let tempMsgs = state.msgs[sessionId]
    let lastMsgIndex = tempMsgs.length - 1
    if (tempMsgs.length === 0 || msg.time >= tempMsgs[lastMsgIndex].time) {
      tempMsgs.push(msg)
    } else {
      for (let i = lastMsgIndex; i >= 0; i--) {
        let currMsg = tempMsgs[i]
        if (msg.time >= currMsg.time) {
          state.msgs[sessionId].splice(i, 0, msg)
          break
        }
      }
    }
  },
  // 用idClient更新消息，目前用于消息撤回
  updateMsgByIdClient (state, msgs) {
    if (!Array.isArray(msgs)) {
      msgs = [msgs]
    }
    let tempTime = new Date().getTime()
    msgs.forEach(msg => {
      // 有idClient且5分钟以内的消息
      if (msg.idClient && tempTime - msg.time < 1000 * 300) {
        state.msgsMap[msg.idClient] = msg
      }
    })
  },
  // 更新当前会话列表的聊天记录，包括历史消息、单聊消息等
  updateCurrSessionMsgs (state, obj) {
    let type = obj.type || ''
    let store = Vue.prototype.$store
    if (type === 'destroy') {
      // 清空会话消息
      state.currSessionMsgs = []
      state.currSessionLastMsg = null
      store.commit('imchat/updateCurrSessionId', {
        type: 'destroy'
      })
    } else if (type === 'init') {
      // 初始化会话消息列表
      if (state.currSessionId) {
        let sessionId = state.currSessionId
        let currSessionMsgs = [].concat(state.msgs[sessionId] || [])
        // 消息截断
        let limit = 36
        let msgLen = currSessionMsgs.length
        if (msgLen - limit > 0) {
          // 设置最后一条消息
          state.currSessionLastMsg = currSessionMsgs[msgLen - limit]
          currSessionMsgs = currSessionMsgs.slice(msgLen - limit, msgLen)
        } else if (msgLen > 0) {
          state.currSessionLastMsg = currSessionMsgs[0]
        } else {
          state.currSessionLastMsg = null
        }
        state.currSessionMsgs = []
        let lastMsgTime = 0
        currSessionMsgs.forEach(msg => {
          if (msg.time - lastMsgTime > 1000 * 300) {
            lastMsgTime = msg.time
            // 5分钟间隔 添加文字时间戳
            state.currSessionMsgs.push({
              type: 'timeTag',
              text: util.formatDate(msg.time, false),
              idClient: msg.time
            })
          }
          state.currSessionMsgs.push(msg)
        })
      }
    } else if (type === 'put') {
      // 追加一条消息
      let newMsg = obj.msg
      let lastMsgTime = 0
      let lenCurrMsgs = state.currSessionMsgs.length
      if (lenCurrMsgs > 0) {
        lastMsgTime = state.currSessionMsgs[lenCurrMsgs - 1].time
      }
      if (newMsg) {
        if (newMsg.time - lastMsgTime > 1000 * 300) {
          state.currSessionMsgs.push({
            type: 'timeTag',
            text: util.formatDate(newMsg.time, false),
            idClient: newMsg.time
          })
        }
        state.currSessionMsgs.push(newMsg)
      }
    } else if (type === 'concat') {
      // 一般用于历史消息的拼接
      let currSessionMsgs = []
      let lastMsgTime = 0
      obj.msgs.forEach(msg => {
        if (msg.time - lastMsgTime > 1000 * 300) {
          lastMsgTime = msg.time
          currSessionMsgs.push({
            type: 'timeTag',
            text: util.formatDate(msg.time, false),
            idClient: msg.time
          })
        }
        currSessionMsgs.push(msg)
      })
      currSessionMsgs.reverse()
      currSessionMsgs.forEach(msg => {
        state.currSessionMsgs.unshift(msg)
      })
      if (obj.msgs[0]) {
        state.currSessionLastMsg = obj.msgs[0]
      }
    } else if (type === 'replace') {
      let msgLen = state.currSessionMsgs.length
      let lastMsgIndex = msgLen - 1
      if (msgLen > 0) {
        for (let i = lastMsgIndex; i >= 0; i--) {
          if (state.currSessionMsgs[i].idClient === obj.idClient) {
            state.currSessionMsgs.splice(i, 1, obj.msg)
            break
          }
        }
      }
    }
  },
  // 初始化、收到离线漫游消息时调用
  updateMsgs (state, msgs) {
    const nim = NIM
    let tempSessionMap = {}
    let store = Vue.prototype.$store
    msgs.forEach(msg => {
      let sessionId = msg.sessionId
      tempSessionMap[sessionId] = true
      if (!state.msgs[sessionId]) {
        state.msgs[sessionId] = []
      }
      // 去重
      state.msgs[sessionId] = nim.mergeMsgs(state.msgs[sessionId], [msg])
    })
    store.commit('imchat/updateMsgByIdClient', msgs)
    for (let sessionId in tempSessionMap) {
      state.msgs[sessionId].sort((a, b) => {
        return a.time - b.time
      })
      if (sessionId === state.currSessionId) {
        store.commit('imchat/updateCurrSessionMsgs', {
          type: 'init'
        })
      }
    }
  },
  // 替换消息列表消息，如消息撤回
  replaceMsg (state, obj) {
    let { sessionId, idClient, msg } = obj
    let tempMsgs = state.msgs[sessionId]
    if (!tempMsgs || tempMsgs.length === 0) {
      return
    }
    let lastMsgIndex = tempMsgs.length - 1
    for (let i = lastMsgIndex; i >= 0; i--) {
      let currMsg = tempMsgs[i]
      if (idClient === currMsg.idClient) {
        state.msgs[sessionId].splice(i, 1, msg)
        break
      }
    }
  },
  updateIMStatus (state, obj) {
    let { status } = obj
    state.status = status
  },
  updateMsgsUnread (state, unread) {
    state.unread = unread
  },
  setNoMoreHistoryMsgs (state) {
    state.noMoreHistoryMsgs = true
  },
  resetNoMoreHistoryMsgs (state) {
    state.noMoreHistoryMsgs = false
  },
  updateFullscreenImage (state, obj) {
    obj = obj || {}
    if (obj.src && obj.type === 'show') {
      state.fullscreenImgSrc = obj.src
      state.isFullscreenImgShow = true
    } else if (obj.type === 'hide') {
      state.fullscreenImgSrc = ' '
      state.isFullscreenImgShow = false
    }
  },
  updateCurrentAudio (state, obj) {
    obj = obj || {}
    state.currentAudio = obj.currentAudio
    state.audioMd5 = obj.md5
  },
  setUser (state, user) {
    state.user = user // 将userInfo存储到vuex中
  },
  updateonOfflineMsgsList (state, obj) {
    state.onOfflineMsgsList = obj
  },
  initSessions (state, obj) {
    state.initSessions = obj
  },
  currImTitleInfo (state, obj) {
    state.currImTitleInfo = obj
  },
  setUserInfos (state, obj) {
    state.userInfos = obj
  },
  crtCallUser (state, obj) {
    state.crtCallUser = obj
  },
  setCurrentStuId (state, id) {
    state.currentStuId = id
  },
  setAvatar (state, url) {
    state.avatarUrl = url
  },
  setVideoCall (state, obj) {
    state.videoCall = obj
  },
  setCurrShowSessionList (state, obj) {
    state.currShowSessionList = obj
  },
  setClassInfoShow (state, obj) {
    state.classInfoShow = obj
  },
  setMsgRecordShow (state, obj) {
    state.msgRecordShow = obj
  },
  setAgent (state, obj) {
    state.agent = obj
  }
}
