/* initNimSDK.js */
function initNimSDK ({ state, commit, dispatch, rootState }, loginInfo) {
  // if (NIM) {
  //   NIM.disconnect()
  // }
  store = Vue.prototype.$store
  const nim = state.nimSDK.getInstance({
    debug: false,
    appKey: config.nimKey[env], // 45c6af3c98409b18a84451215d0bdd6e
    account: loginInfo.accid, // tal-ls
    token: loginInfo.token, // 36f17c3939ac3e7b2fc9396fa8e953ea
    // appKey: '45c6af3c98409b18a84451215d0bdd6e',
    // account: 'chengji',
    // token: '57413284b35591879b8bae4bd5029625',
    db: true,
    syncSessionUnread: true,
    syncMsgReceipts: true,
    autoMarkRead: true,
    syncRelations: true,
    onmutelist: onMutelist,
    onsyncmarkinmutelist: onMarkInMutelist,
    syncFriends: false,
    syncFriendUsers: false,
    syncTeams: false,
    syncExtraTeamInfo: false,
    syncTeamMembers: false,
    onconnect: () => {
      console.log('connect success...')
      // dispatch('initNimWebRTC')
      // dispatch('NimCall')
      try {
        if (loginInfo) {
          commit('updateIMStatus', { status: '正在同步会话列表...' })
          commit('updateUserUID', loginInfo)
          commit('updateKicked', false) // 重连成功后，更新被踢状态
          nim.getUser({
            account: loginInfo.accid,
            done: (err, data) => {
              if (err) {
                console.error(err)
              } else {
                dispatch('setUser', data)
                commit('setAvatar', data.avatar)
              }
            }
          })
        }
      } catch (e) {
        // window.errorReport.post({
        //   message: 'IM onconnect recall',
        //   error: e
        // })
      }
    },
    onerror: function onError (error, obj) {
      console.error(error)
      // window.errorReport.post({
      //   message: 'IM onerror',
      //   error
      // })
      commit('updateIsIMConnecting', false)
      // $(document).trigger('visibilitychange.im')
    },
    onwillreconnect: function onWillReconnect () {
      Vue.prototype.$message({
        message: '聊天会话正在恢复，请检查网路是否断开......',
        type: 'warning'
      })
    },
    ondisconnect: function onDisconnect (error) {
      console.log('-----------', error)
      switch (error.code) {
        // 账号或者密码错误
        case 302:
          dispatch('connect', true)
          break
        case 416:
        case 417:
          commit('updateKicked', true)
          break
        case 'kicked':
          commit('updateKicked', true)
          break
        case 'logout':
          // 主动退出，不做处理
          break
        default:
          // window.errorReport.post({
          //   message: 'IM ondisconnect switch default',
          //   error
          // })
          // dispatch('connect', true)
          break
      }
    },
    // 会话
    onsessions: onSessions,
    onupdatesession: onUpdateSession,
    // 消息
    onroamingmsgs: onRoamingMsgs,
    onofflinemsgs: onOfflineMsgs,
    onmsg: onMsg,
    onsysmsg: onSysMsg,
    onofflinesysmsgs: onofflineSysMsgs,
    onroamingsysmsgs: onroamingSysMsgs,
    // onsysmsgunread: onSysMsgUnread,
    // onupdatesysmsgunread: onSysMsgUnreapd,
    onsyncdone: function onSyncDone () {
      // 说明在聊天列表页
      // 此处很重要，是为了解决在各标签页能够找到之前接收到的未读消息
      // 否则，虽然已有了新消息，但不刷新页面则并不会通知，因为在其他页面已经通知过了，服务器端会自动标记为已读而不会重复推送
      // 将IM连接的状态修改设置到onsyncdone中，确保不会再出现能打开IM，但是会话列表却为空的情况
      commit('updateKicked', false)
      commit('updateIMStatus', { status: 'sucess' })
      commit('updateIsIMConnecting', false)
      if (state.currSessionId) {
        dispatch('setCurrSession', state.currSessionId)
      }
    }
  })
  setNIM(nim)
}
