function onMsg (msg) {
  console.log('onMsg', msg)
  let cust = {}
  if (msg.fromClientType && msg.fromClientType === 'Server') {
    if (msg.custom) {
      cust = JSON.parse(msg.custom)
      if (cust.messageType === '15') { // 是否在线探测
        myIdentity()
        return
      }
      if (cust.messageType === '16') { //  是否语音探测
        if (store.state.imchat.calling || store.state.imchat.beCalling || store.state.imchat.callOther) {
          console.log('正在通话中...')
        } else {
          syncAviContact({
            nimAccid: store.state.roleRelativeData.roleInfo.nimAccid,
            launchType: '3'
          })
        }
        return
      }
    }
  }
  if (cust.messageType !== '19') { // 当不是群发消息时刷新session列表 19 群发消息 20 欢迎语
    reLoadSession([ { id: `p2p-${msg.to}`, to: msg.to } ])
  }
  store.commit('imchat/putMsg', msg)
  if (msg.sessionId === store.state.imchat.currSessionId) {
    store.commit('imchat/updateCurrSessionMsgs', {
      type: 'put',
      msg
    })
    // 当im窗口展开时发送已读回执
    if (!store.state.imchat.isShowMini) {
      store.dispatch('imchat/sendMsgReceipt')
      store.dispatch('imchat/resetSessionUnread')
    }
  }
}

function updateMsgsUnread ({ commit }, sessions) {
  let unread = 0
  sessions.forEach(session => {
    unread += session.unread || 0
  })
  commit('updateMsgsUnread', unread)
}

function onRevocateMsg (error, msg) {
  const nim = NIM
  if (error) {
    let errMsg = ''
    if (error.code === 508) {
      errMsg = '发送时间超过2分钟的消息，不能被撤回'
    } else {
      errMsg = '消息撤回失败，请确认后重试'
    }
    Vue.prototype.$message(errMsg)
    return
  }
  let tip = ''
  let to = ''
  if (msg.from === store.state.imchat.userUID) {
    tip = '你撤回了一条消息'
    to = msg.to
  } else {
    to = msg.from
    const userInfo = store.state.imchat.userInfos[msg.from]
    if (userInfo) {
      tip = `${util.getFriendAlias(userInfo)}撤回了一条消息`
    } else {
      tip = '对方撤回了一条消息'
    }
  }
  nim.sendTipMsg({
    isLocal: true,
    scene: msg.scene,
    to,
    tip,
    time: msg.time,
    done: function sendTipMsgDone (error, tipMsg) {
      if (error) {
        console.log(error)
        return
      }
      const idClient = msg.deletedIdClient || msg.idClient
      store.commit('imchat/replaceMsg', {
        sessionId: msg.sessionId,
        idClient,
        msg: tipMsg
      })
      if (msg.sessionId === store.state.imchat.currSessionId) {
        store.commit('imchat/updateCurrSessionMsgs', {
          type: 'replace',
          idClient,
          msg: tipMsg
        })
      }
    }
  })
}

function revocateMsg ({ state, commit }, msg) {
  const nim = NIM
  const { idClient } = msg
  if (!state.msgsMap[idClient]) {
    Vue.prototype.$message('发送时间超过2分钟的消息，不能被撤回')
    return
  }
  msg = Object.assign(msg, state.msgsMap[idClient])
  nim.deleteMsg({
    msg,
    done: function deleteMsgDone (error) {
      onRevocateMsg(error, msg)
    }
  })
}

function onSendMsgDone (error, msg) {
  if (error) {
    // 被拉黑
    vue.$message.error('消息发送失败，请稍后重试')
    store.dispatch('imchat/connect', true)
    // window.errorReport.post({
    //   message: 'SendMsgError',
    //   error
    // })
  }
  if (msg.status && msg.status === 'fail') {
    return
  }
  store.commit('imchat/setClassInfoShow', false)
  console.log('onSendMsgDone')
  onMsg(msg)
}
function sendMsg ({ state, commit, rootState }, obj) {
  const nim = NIM
  obj = obj || {}
  nim.sendText({
    scene: obj.scene,
    to: obj.to,
    text: obj.text,
    // app要求的字段
    pushPayload: JSON.stringify({
      pushTitle: rootState.roleRelativeData.userInfo.crmRoleId === 8 ? '在线老师1v1咨询' : '家庭学习顾问',
      xrsUrl: 'IMSession',
      sessionId: state.userUID,
      consultId: state.currImTitleInfo.id,
      advisorType: rootState.roleRelativeData.userInfo.crmRoleId
    }),
    pushContent: obj.text,
    done: onSendMsgDone
  })
}

function sendCustomMsg ({ state, commit, rootState }, obj) {
  const nim = NIM
  obj = obj || {}
  nim.sendCustomMsg({
    scene: 'p2p',
    to: obj.to,
    content: JSON.stringify(obj.content),
    // app要求的字段
    pushPayload: JSON.stringify({
      pushTitle: rootState.roleRelativeData.userInfo.crmRoleId === 8 ? '在线老师1v1咨询' : '家庭学习顾问',
      xrsUrl: 'IMSession',
      sessionId: state.userUID,
      consultId: state.currImTitleInfo.id,
      advisorType: rootState.roleRelativeData.userInfo.crmRoleId
    }),
    pushContent: obj.pushContent,
    done: onSendMsgDone
  })
}

function sendTextMessage (
  scene,
  to,
  text,
  isLocal,
  callback
) {
  isLocal = !!isLocal
  var options = {
    scene: scene || 'p2p',
    to: to,
    text: text,
    isLocal: isLocal,
    done: callback
  }
  NIM.sendText(options)
}
function sendLocalMessage ({ state, commit, dispatch }, text, to) {
  if (!to) to = webrtc.netcallAccount
  setTimeout(() => {
    sendTextMessage('p2p', to, text, true, onSendMsgDone)
  }, 100)
}

// 发送文件消息
function sendFileMsg ({ state, commit, dispatch }, obj) {
  const nim = NIM
  const { scene, to, fileInput } = obj
  let type = 'file'
  if (/\.(png|jpg|bmp|jpeg|gif)$/i.test(fileInput.value)) {
    type = 'image'
  } else if (/\.(mov|mp4|ogg|webm)$/i.test(fileInput.value)) {
    type = 'video'
  }
  dispatch('setProcess', 0)
  dispatch('showLoading')
  nim.sendFile({
    scene,
    to,
    type,
    fileInput,
    uploadprogress: function (data) {
      dispatch('setProcess', data.percentage || 0)
    },
    uploaderror: function () {
      console.log('上传失败')
    },
    uploaddone: function (error, file) {
      if (error) {
        console.log(error)
      }
    },
    beforesend: function (msg) {
      // console && console.log('正在发送消息, id=', msg);
    },
    done: function (error, msg) {
      onSendMsgDone(error, msg)
      fileInput.value = ''
      dispatch('hideLoading')
    }
  })
}

function uploadImg ({ state, commit, dispatch }, obj) {
  const nim = NIM
  const { fileInput } = obj
  let type = 'file'
  if (/\.(png|jpg|bmp|jpeg|gif)$/i.test(fileInput.value)) {
    type = 'image'
  } else if (/\.(mov|mp4|ogg|webm)$/i.test(fileInput.value)) {
    type = 'video'
  }
  // dispatch('showLoading')
  return new Promise((resolve, reject) => {
    nim.previewFile({
      type,
      fileInput,
      commonUpload: true,
      uploadprogress: function (obj) {
        console.log('文件总大小: ' + obj.total + 'bytes')
        console.log('已经上传的大小: ' + obj.loaded + 'bytes')
        console.log('上传进度: ' + obj.percentage)
        console.log('上传进度文本: ' + obj.percentageText)
      },
      done: function (error, file) {
        console.log('上传image' + (!error ? '成功' : '失败'))
        dispatch('hideLoading')
        if (error) {
          Vue.prototype.$message.error(error.message)
          return // Message(error.message)
        }
        // show file to the user
        // uploaded(file)
        resolve(file)
      }
    })
  })
}
function sendMsgReceipt ({ state, commit }) {
  // 如果有当前会话
  const currSessionId = state.currSessionId
  if (currSessionId) {
    // 只有点对点消息才发已读回执
    if (util.parseSession(currSessionId).scene === 'p2p') {
      // let msgs = store.state.imchat.currSessionMsgs;
      const nim = NIM
      if (state.sessionMap[currSessionId]) {
        nim.sendMsgReceipt({
          msg: state.sessionMap[currSessionId].lastMsg,
          done: function sendMsgReceiptDone (error, obj) {
            console.log(
              '发送消息已读回执' + (!error ? '成功' : '失败'),
              error,
              obj
            )
          }
        })
      }
    }
  }
}

function getHistoryMsgs ({ state, commit, dispatch }, obj) {
  const nim = NIM
  if (nim) {
    const { scene, to } = obj
    let options = {
      scene,
      to,
      reverse: false,
      asc: true,
      limit: 36,
      done: function getHistoryMsgsDone (error, obj) {
        if (error) {
          // commit('updateKicked', true)
          return
        }
        if (obj.msgs) {
          if (obj.msgs.length === 0) {
            commit('setNoMoreHistoryMsgs')
          } else {
            commit('updateCurrSessionMsgs', {
              type: 'concat',
              msgs: obj.msgs
            })
          }
        }
      }
    }
    if (state.currSessionLastMsg) {
      options = Object.assign(options, {
        lastMsgId: state.currSessionLastMsg.idServer,
        endTime: state.currSessionLastMsg.time
      })
    }
    nim.getHistoryMsgs(options)
  }
}

function resetNoMoreHistoryMsgs ({ commit }) {
  commit('resetNoMoreHistoryMsgs')
}

function audioPlay (url, md5) {
  const currentAudio = new Audio(url)
  currentAudio.play()
  currentAudio.onended = () => {
    store.commit('imchat/updateCurrentAudio', {
      currentAudio: null,
      md5: ''
    })
  }
  store.commit('imchat/updateCurrentAudio', {
    currentAudio,
    md5
  })
}

function playAudio ({ state, commit }, obj) {
  const { url, md5 } = obj
  if (!state.audioMd5) {
    audioPlay(url, md5)
  } else if (state.audioMd5 !== md5) {
    state.currentAudio.pause()
    state.currentAudio.currentTime = 0
    audioPlay(url, md5)
  } else if (state.audioMd5 === md5) {
    state.currentAudio.pause()
    state.currentAudio.currentTime = 0
    commit('updateCurrentAudio', {
      currentAudio: null,
      md5: ''
    })
  }
}

/* mutes.js */

function onMutelist (mutes) {
  mutes = mutes.map(item => {
    if (typeof item.isMute !== 'boolean') {
      item.isMute = true
    }
    item.scene = 'p2p'
    item.id = `p2p-${item.account}`
    item.to = item.account
    return item
  })
  // console.log('-----------------------', mutes)
  onSessions(mutes)
  store.commit('imchat/updateMuteUserInfo', mutes)
}

function onMarkInMutelist (obj) {
  obj = obj || {}
  const account = obj.account
  if (account === store.state.imchat.userUID) {
  } else {
    if (typeof obj.isAdd === 'boolean') {
      onMutelist([
        {
          account,
          isMute: obj.isAdd
        }
      ])
    }
  }
}

function updateMute ({ state }, { account, isMute }) {
  const nim = NIM
  if (account && typeof isMute === 'boolean') {
    nim.markInMutelist({
      account: account.replace(/^p2p-/, ''),
      isAdd: isMute,
      done: function (error, obj) {
        if (error) {
          console.log(error)
          return
        }
        onMarkInMutelist(obj)
      }
    })
  }
}

