/**
 * 获取资源列表常用属性筛选列表
 * @return
 *   belongStatusList 绑定状态，1未绑定，2已绑定 array 联动查询返回参数
 *   belongTypeList 归属类型，0无，11城市，13服务中心，14呼叫中心 array 联动查询返回参数
 *   customActiveStatusList 客户状态，1活跃，2预警，3沉睡 array 联动查询返回参数
 *   distributeStatusList 分配状态，1未分配，2已分配 array 联动查询返回参数
 *   readStatusList 在读状态，1未在读，2历史在读，3在读 array 联动查询返回参数
 *   statusList
 */
export function queryResourceCondition (params) {
  return axios.request({
    url: '/crm/queryResourceCondition',
    method: 'get',
    params
  })
}

