<template>
  <div class="wrapper">
    <div class="title">
      <span>历史沟通记录</span>
      <el-icon class="el-icon-close" @click.native="close"></el-icon>
    </div>
    <div class="record-wrapper">
      <ButtonGroup vertical>
        <Button v-for="item in advList" :type="advAccid === item.nimAccid ? 'primary' : 'default'" @click="getMsg(item.nimAccid)" :key="item.nimAccid" style="text-align: left">
          <span class="tag" size="mini" v-if="item.isOriginal">绑定</span>
          <span>{{item.userName}}</span>
        </Button>
      </ButtonGroup>
      <ul id="record-content" class="record-content" ref="chatContent">
        <RecordItem v-for="item in msgList" :key="item.msgid" :rawMsg="item" @msg-loaded="msgLoaded"></RecordItem>
      </ul>
    </div>
  </div>
</template>

<script>
import { queryRecentContactList, querySessionMsg } from '@/api/advisor'
import { mapState } from 'vuex'
import RecordItem from './RecordItem'
export default {
  name: 'MsgRecord',
  components: {
    RecordItem
  },
  data () {
    return {
      advAccid: '',
      advList: [], // 顾问列表
      msgList: [] // 选修列表
    }
  },
  methods: {
    queryAdvLIst () {
      queryRecentContactList({
        pyUid: this.currImTitleInfo.pyUid,
        accId: this.currImTitleInfo.nimAccid
      }).then(data => {
        this.advList = data
        if (this.advList.length) {
          this.getMsg(this.advList[0].nimAccid)
        }
      }).catch(err => {
        this.$Message.error('查询历史会话联系人错误：' + err.message)
      })
    },
    getMsg (accid) {
      this.advAccid = accid
      querySessionMsg({
        advisorAccid: accid,
        studentAccid: this.currImTitleInfo.nimAccid
      }).then(data => {
        console.log(data)
        this.msgList = data.map(d => {
          switch (d.type) {
            case 0:
              d.type = 'text'
              d.text = d.body.msg
              break
            case 1:
              d.type = 'image'
              d.file = {
                url: d.body.url
              }
              break
            case 2:
              d.type = 'audio'
              d.file = {
                mp3Url: d.body.url,
                dur: d.body.dur
              }
              break
            case 3:
              d.type = 'video'
              break
            case 5:
              d.type = 'notification'
              d.duration = d.body.data.duration
              break
            case 6:
              d.type = 'notification'
              break
            case 10:
              d.type = 'tip'
              d.tip = d.body.msg
              break
            case 100:
              d.type = 'custom'
              d.content = d.body
              break
          }
          d.flow = d.from === this.currImTitleInfo.nimAccid ? 'in' : 'out'
          return d
        })
        // this.$nextTick(() => {
        //   this.$refs.chatContent.scrollTop = this.$refs.chatContent.scrollHeight
        // })
      }).catch(err => {
        this.$Message.error('查询历史会话错误：' + err.message)
      })
    },
    close () {
      this.$store.commit('imchat/setMsgRecordShow', false)
    },
    msgLoaded () {
      clearTimeout(this.msgLoadedTimer)
      this.msgLoadedTimer = setTimeout(() => {
        this.$refs.chatContent.scrollTop = this.$refs.chatContent.scrollHeight
      }, 50)
    }
  },
  computed: {
    ...mapState({
      currImTitleInfo: state => state.imchat.currImTitleInfo,
      userUID: state => state.imchat.userUID
    })
  },
  mounted () {
    this.queryAdvLIst()
  }
}
</script>

