let ms = {}

function getItem (key) {
  return key in ms ? ms[key] : null
}

function setItem (key, value) {
  ms[key] = value
  return value
}

function removeItem (key) {
  let found = key in ms
  if (found) {
    return delete ms[key]
  }
  return false
}

function clear () {
  ms = {}
  return true
}

export default {
  getItem: getItem,
  setItem: setItem,
  removeItem: removeItem,
  clear: clear
}
