<template>
  <li
    :class="{
      'item-time': msg.type === 'timeTag',
      'item-tip': msg.type === 'tip'
    }"
    class="im-msg clearfix">
    <el-avatar v-if="msg.flow === 'in'" style="float: left;margin-right: 5px" size="small" :src="avatarUrl"></el-avatar>
    <el-avatar v-if="msg.flow === 'out'"
               @click.native="$emit('editInfo')"
               style="float: right;margin-left: 5px;cursor: pointer"
               size="small"
               :src="user.avatar || defaultAvatar"></el-avatar>
    <div v-if="msg.type === 'timeTag'">---- {{ msg.showText }} ----</div>
    <div
      v-else-if="msg.type === 'tip'"
      class="tip">{{ msg.showText }}</div>
    <div
      v-else-if="msg.flow === 'in' || msg.flow === 'out'"
      :class="{
        'pull-left': msg.flow === 'in',
        'pull-right': msg.flow === 'out'
      }"
      class="text">
      <span
        v-if="msg.type === 'text' || msg.type === 'notification'"
        v-html="msg.showText"/>
      <span
        v-else-if="msg.type === 'audio'"
        ref="mediaMsg"
        class="text audio"
        @click="playAudio(msg)"><i
          :class="{'glyphicon-play': playingAudioMd5 !== msg.file.md5, 'glyphicon-pause': playingAudioMd5 === msg.file.md5}"
          class="glyphicon"/>{{ (msg.file.dur / 1000).toFixed(2) }}"</span>
      <span
        v-else-if="msg.type === 'image'"
        ref="mediaMsg"
        class="text image"/>
      <span
        v-else-if="msg.type === 'video'"
        ref="mediaMsg"
        class="text video"/>
      <span
        v-else-if="msg.type === 'file'"
        class="text file">
        <section class="tal-file-icon" :class="{'pdf': msg.file.ext.toLowerCase() === 'pdf', 'word': msg.file.ext.toLowerCase().includes('doc'), 'excel': msg.file.ext.toLowerCase().includes('xls')}" ></section>
        <a
          :href="msg.fileLink"
          :download="msg.showText"
          target="_blank">
          <i class="glyphicon glyphicon-file"/>{{ msg.showText }}
        </a>
      </span>
      <span v-else-if="msg.type === 'custom' && msg.msgType === 40" class="text">
        <a @click="searchClass(msg.classId)">
          {{ msg.showText }}
        </a>
      </span>
      <span v-else class="text">
        未知消息类型：{{msg.type}}
      </span>
      <div
        v-if="msg.status==='fail'"
        class="msg-failed"><span class="weui-icon-warn">!</span></div>
      <div v-else-if="msg.flow === 'out'">
        <div
          v-if="msg.flow === 'out' && !msg.isLocal"
          :class="{
            'receipt-done': isMsgRemoteRead,
            'receipt-will': !isMsgRemoteRead
          }"
          class="receipt"
        >
          {{ isMsgRemoteRead ? '已读': '未读' }}
        </div>
        <div
          v-if="msg.flow === 'out' && !msg.isLocal"
          class="msg-more">
          <el-dropdown
            id="dropdown"
            size="mini"
            trigger="click"
            @command="revocateMsg">
            <span class="el-dropdown-link">
              ...
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item :command="msg">撤回</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
    </div>
    <div
      v-if="msg.type === 'deleteMsg'"
      class="text system">
      对方撤回了一条消息
    </div>
  </li>
</template>

<script>
import util from '@/common/util'
import emojiObj from '@/assets/js/emoji'
export default {
  props: {
    rawMsg: {
      type: Object,
      default () {
        return {}
      }
    }
  },
  data () {
    return {
      defaultAvatar: 'https://nim-nosdn.netease.im/NDI2NjkwMg==/bmltYV8xNzQyNzU4MDA4NF8xNTg2ODUwMjU0ODgxX2IzNTk2MjAyLTZjNmEtNDU0Yi1iZWZlLWQxZmM0YTQ2Y2Q4Yg==?createTime=1586850254744',
      msg: '',
      isFullImgShow: false,
      currentAudio: null,
      isMsgRemoteRead: false
    }
  },
  computed: {
    nim () {
      return this.$store.state.imchat.nim
    },
    currSessionId () {
      return this.$store.state.imchat.currSessionId
    },
    playingAudioMd5 () {
      return this.$store.state.imchat.audioMd5
    },
    user () {
      return this.$store.state.imchat.user
    },
    userInfos () {
      return this.$store.state.imchat.userInfos
    },
    avatarUrl () {
      const account = this.$store.state.imchat.currSessionId.split('-')[1]
      return this.$store.state.imchat.userInfos[account].avatar || 'https://nim-nosdn.netease.im/NDI2NjkwMg==/bmltYV8xNzQyNzU4MDA4NF8xNTg3MTA4NTE3MTY0Xzg0M2UyZmYyLTQ0ZDAtNGMzYy1iYmZkLTE3NjMxZDYyNDA4Yw==?createTime=1587108517904'
    }
  },
  beforeMount () {
    let item = Object.assign({}, this.rawMsg)
    if (item.type === 'timeTag') {
      item.showText = item.text
    } else if (item.type === 'text') {
      item.showText = util.escape(item.text)
      if (/\[[^\]]+\]/.test(item.showText)) {
        let emojiItems = item.showText.match(/\[[^\]]+\]/g)
        emojiItems.forEach(text => {
          let emojiCnt = emojiObj.emojiList.emoji
          if (emojiCnt[text]) {
            item.showText = item.showText.replace(
              text,
              `<img width="20" src="${emojiCnt[text].img}">`
            )
          }
        })
      }
      if (item.text === 'OFFLINE_NETCALL_MISS') {
        item.showText = '离线通话未接听'
      } else if (item.text === 'HIDDEN_MSG_FOR_UNREAD') {
        item.type = 'tip'
        item.showText = '系统提示：通话未接听'
      }
    } else if (item.type === 'image') {
      item.originLink = item.file.url
    } else if (item.type === 'audio') {
      item.audioSrc = item.file.mp3Url
      item.showText = Math.round(item.file.dur / 1000) + '" 点击播放'
    } else if (item.type === 'file') {
      item.fileLink = item.file.url
      item.showText = item.file.name
    } else if (item.type === 'tip') {
      item.showText = item.tip
    } else if (item.type === 'notification') {
      if (item.attach) {
        switch (item.attach.type) {
          case 'netcallMiss':
            item.showText = '对方拨打通话未接听'
            break
          case 'netcallBill':
            item.showText = '通话' + (item.flow === 'in' ? '接听' : '拨打') + '时长 ' + this.getNetcallDurationText(item.attach.duration)
            break
          case 'cancelNetcallBeforeAccept':
            item.showText = '对方无人接听'
            break
          case 'rejectNetcall':
            item.showText = '已拒绝'
            break
          case 'netcallRejected':
            item.showText = '对方已拒绝'
            break
        }
      } else {
        item.showText = item.text
      }
    } else if (item.type === 'custom') {
      const content = JSON.parse(item.content)
      if (content.type === 40) {
        item.msgType = 40
        item.classId = content.data.classId
        item.showText = content.data.className
      }
    } else {
      item.showText = `[${util.mapMsgType(item)}],请到手机或电脑客户端查看`
    }

    const msgRemoteRead = this.$store.getters['imchat/getMsgReceiptTime']()
    if (msgRemoteRead >= item.time) {
      this.isMsgRemoteRead = true
    } else {
      const timer = setInterval(() => {
        let msgReceiptTime = this.$store.getters['imchat/getMsgReceiptTime']()
        if (msgReceiptTime >= item.time) {
          this.isMsgRemoteRead = true
          clearInterval(timer)
        }
      }, 50)
    }
    this.msg = item
  },
  mounted () {
    let item = this.msg
    this.$nextTick(() => {
      let media = null
      if (item.type === 'image') {
        // 图片消息缩略图
        media = new Image()
        media.className = 'imImage'
        media.src = item.file.url + '?imageView&thumbnail=180x0&quality=85'
        // media.src = item.file.url
        media.style.width = '100%'
        media.style.height = 'auto'
      } else if (item.type === 'video') {
        if (/(mov|mp4|ogg|webm)/i.test(item.file.ext)) {
          media = document.createElement('video')
          media.src = item.file.url
          media.width = 1280
          media.hieght = 960
          media.autoStart = false
          media.preload = 'metadata'
          media.controls = 'controls'
        } else {
          let aLink = document.createElement('a')
          aLink.href = item.file.url
          aLink.target = '_blank'
          aLink.innerHTML = `<i class="u-icon icon-file"></i>`
          this.$refs.mediaMsg.appendChild(aLink)
        }
      }
      if (media) {
        if (this.$refs.mediaMsg) {
          this.$refs.mediaMsg.appendChild(media)
          if (item.type === 'image') {
            this.$store.dispatch('imchat/initViewer')
          }
        }
        media.onload = () => {
          this.$emit('msg-loaded')
        }
        media.onerror = () => {
          this.$emit('msg-loaded')
        }
      } else {
        this.$emit('msg-loaded')
      }
    })
  },
  methods: {
    revocateMsg (msg) {
      if (this.currSessionId && msg.flow === 'out') {
        this.$store.dispatch('imchat/revocateMsg', {
          idClient: msg.idClient
        })
      }
    },
    showFullImg (src) {
      this.$store.dispatch('imchat/showFullscreenImg', {
        src
      })
    },
    playAudio (msg) {
      const file = msg.file || {}
      this.$store.dispatch('imchat/playAudio', {
        url: msg.audioSrc,
        md5: file.md5
      })
    },
    getNetcallDurationText (allSeconds) {
      var result = ''
      var hours, minutes, seconds
      if (allSeconds >= 3600) {
        hours = parseInt(allSeconds / 3600)
        result += ('00' + hours).slice(-2) + ' : '
      }
      if (allSeconds >= 60) {
        minutes = parseInt(allSeconds % 3600 / 60)
        result += ('00' + minutes).slice(-2) + ' : '
      } else {
        result += '00 : '
      }
      seconds = parseInt(allSeconds % 3600 % 60)
      result += ('00' + seconds).slice(-2)
      return result
    },
    searchClass (classId) {
      const userInfo = this.userInfos[this.currSessionId.replace(/^p2p-/, '')]
      const option = {
        name: 'applyWorkbenchForMulti',
        query: {
          cityCode: userInfo.areaCode,
          card: userInfo.stuLoginName,
          classId
        },
        params: {
          id: this.currSessionId + '-' + classId
        }
      }
      console.log(option)
      this.$router.push(option)
    }
  }
}
</script>

