    float: right;
  }
  .msg-failed {
    position: absolute;
    left: -20px;
    top: 2px;
    margin-right: 0.2rem;
    vertical-align: top;
    font-size: 0.92rem;
    line-height: 1.5rem;
    display: inline-block;
    width: 1.5rem;
    border-radius: 50%;
    background-color: red;
    color: white;
    font-weight: bold;
  }
  .msg-time {
    position: absolute;
    top: -19px;
    right: -12px;
    text-align: right;
    width: 120px;
    padding: 2px;
    color: #aaa;
    transform: scale(0.8);
    display: none;
  }
  .msg-more {
    position: absolute;
    top: 2px;
    left: -20px;
