// 查看学员详情通用方法
export default {
  data () {
    return {

    }
  },
  methods: {
    goCustomerDetail (userInfo, type) {
      this.$router.push({
        name: `applyStuArchivesForMulti`,
        params: {
          id: userInfo.card || userInfo.stuLoginName
        },
        query: {
          card: userInfo.card || userInfo.stuLoginName,
          cityCode: userInfo.cityCode || userInfo.areaCode
        }
      })
    },
    // 点击输出事件和数据
    emitClickHandle (type, data) {
      this.$emit(type, data)
    }
  }
}
