/* eslint-disable prefer-promise-reject-errors */
import axios from 'axios'
import { getPlatform } from './util'
import Cookie from 'js-cookie'
import { apiTrack } from './tal-sensors'
const userInfo = Cookie.get('columbusUserInfo')
const Platform = getPlatform()
class HttpRequest {
  constructor (config) {
    this.baseURL = config.baseURL
    this.withCredentials = config.withCredentials
    this.timeout = config.timeout
    this.queue = {}
  }
  getInsideConfig () {
    return {
      baseURL: this.baseURL,
      withCredentials: this.withCredentials,
      timeout: this.timeout,
      headers: {
        'X-Ca-platform': Platform,
        'X-Ca-platfrom': Platform,
        platform: 'splat',
        creator: userInfo ? JSON.parse(userInfo).id : null,
        empNo: userInfo ? JSON.parse(userInfo).id : null
      }
    }
  }
  destroy (url) {
    delete this.queue[url]
    if (!Object.keys(this.queue).length) {
      // Spin.hide()
    }
  }
  setHeader (config, name, func) {
    if (config.params[name]) {
      config.headers[name] = config.params[name]
    } else if (config.data && config.data[name]) {
      config.headers[name] = config.data[name]
    } else {
      if (func) {
        func()
      }
    }
  }
  getCookieObj (name) {
    return Cookie.get(name) ? JSON.parse(Cookie.get(name)) : {}
  }
  interceptors (instance, url) {
    // 请求拦截
    instance.interceptors.request.use(
      config => {
        // 添加全局的loading...
        if (!Object.keys(this.queue).length) {
          // Spin.show() // 不建议开启，因为界面不友好
        }
        this.queue[url] = true
        config.params = config.params || {}
        config.params.rn = Math.random()
        this.setHeader(config, 'cityCode')
        this.setHeader(config, 'X-Ca-referer')
        this.setHeader(config, 'uid', () => {
          config.headers.uid = this.getCookieObj('currentStudent').uid || ''
        })
        this.setHeader(config, 'identityId')
        config.headers['X-Ca-userId'] = this.getCookieObj('currentStudent').studentId || ''
        if (config.data && config.data['isForm']) {
          config.headers['content-type'] = 'application/x-www-form-urlencoded'
        }
        if (localStorage['authIdentityId']) {
          config.headers['X-Ca-identity'] = localStorage['authIdentityId']
        }

        return config
      },
      error => {
        return Promise.reject(error)
      }
    )
    // 响应拦截
    instance.interceptors.response.use(
      res => {
        const errCode = res.data.code
        /* 一站式网关异常code 需要通知父级 将该code用postmessage 冒出去就可以了 父级接受进行处理 */
        if (errCode === 'ACCESS_DENIED') {
          return Promise.reject({
            message: '您无此功能权限'
          })
        } else if (
          // 一站式网关异常code 需要通知父级 将该code用postmessage 冒出去就可以了 父级接受进行处理 */
          errCode === 'ENCODE_ERROR_INTERNAL' ||
          errCode === 'HTTP_HEADER_REQUIRED' ||
          errCode === 'HTTP_HEADER_VALUE_ERROR' ||
          errCode === 'HTTP_COOKIE_REQUIRED' ||
          errCode === 'HTTP_COOKIE_VALUE_ERROR' ||
          errCode === 'HTTP_HEADER_FORBIDDEN' ||
          errCode === 'HTTP_REQUEST_PATH_FORBIDDEN' ||
          errCode === 'HTTP_REQUEST_ERROR' ||
          errCode === 'TOO_MANY_REQUESTS' ||
          errCode === 'CID_NOT_REGIST' ||
          errCode === 'CID_HAS_NOT_SECURET' ||
          errCode === 'SIGNATURE_NOT_MATCH' ||
          errCode === 'LOAD_PRINCIPAL_ERROR' ||
          errCode === 'LOAD_AUTHORITY_COLLECTION_ERROR' ||
          errCode === 'LOAD_RATELIMITER_ERROR'
        ) {
          // sendErrorToParentFrame(errCode)
          // "_sys_service" 为 系统标识
          if (top === window) {
            localStorage.loginToken = ''
            localStorage.headNavList = ''
            Cookie.remove('columbusJwt')
            Cookie.remove('columbusUserInfo')
            top.location.href = '/login?v=' + Math.random()
          } else {
            window.parent.postMessage(`${errCode}_sys_${Platform}`, '*')
          }
          return res.data
        } else {
          if (
            url === '/upload/getAuth' ||
            url === 'register/exportWaitListExportExcel' ||
            url === '/targetTask/downloadExcel' ||
            url === '/excel/downloadExcel'
          ) {
            if (res.data.error) {
              return Promise.reject({
                message: res.data.message
              })
            } else {
              return res.data
            }
          } else if (
            url.indexOf('crmPanelList/download') !== -1 ||
            url.indexOf('crmPanelList/imChatDownload') !== -1 ||
            url.indexOf('crmPanelList/evaluationExport') !== -1 ||
            url.indexOf('crmPanelList/serviceNotShiftDownload') !== -1
          ) {
            if (res.data.error) {
              return Promise.reject({
                message: res.data.message
              })
            } else {
              return res
            }
          }
          // rlt 为 false 直接提示 message
          // rlt 为 true 状态 000000
          this.destroy(url)
          if (!res.data.rlt) {
            return Promise.reject(
              res.data || {
                message: '系统错误'
              }
            )
          } else {
            if (res.data) {
              return res.data.data
            } else {
              return Promise.reject({
                message: '系统错误'
              })
            }
          }
        }
      },
      error => {
        let jwt = Cookie.get('columbusJwt')
        if (!jwt || jwt === undefined || jwt === 'undefined') {
          top.location.href = '/login?v=' + Math.random()
        } else {
          this.destroy(url)
          return Promise.reject({
            message: error.response.data.message || '前端请求错误'
          })
        }
      }
    )

    // 埋点上报拦截

    apiTrack(instance)
  }
  request (options) {
    const instance = axios.create()
    const defaultConfig = this.getInsideConfig()
    let headers = this.getInsideConfig().headers
    if (options.headers) {
      headers = Object.assign(defaultConfig.headers, options.headers)
    }
    options = Object.assign(defaultConfig, options)
    options.headers = headers
    this.interceptors(instance, options.url)
    return instance(options)
  }
}
export default HttpRequest
