import axios from '@/libs/api.request'
/**
 * 查询当前地域范围内的学校
 * @param params
 * @returns {*}
 */
export const queryAllSchool = (data) => {
  return axios.request({
    url: 'scDic/queryAllSchool',
    data,
    method: 'post'
  })
}

