/** 报班管理--班级查询 列表
 * @param areaId 区域id body false string
 * @param cityId 城市编码 body false string
 * @param claEndCount 剩余课次区间end body false integer(int32)
 * @param claStartCount 剩余课次区间start body false integer(int32)
 * @param classType 授课类型 body false integer(int32)
 * @param classtimeIds 上课时间类型id body false string
 * @param classtimeTimeClassify 上课时间类型 body false string
 * @param classtimeTimePeriod 上课时间 body false string
 * @param classtimeTypeId 上课时间类型id body false string
 * @param endDate 开课日期end body false string
 * @param gradeId 年级id body false string
 * @param gradeTypeId 年部id body false string
 * @param isSchoolTest 是否进行入学测试 body false integer(int32)
 * @param levelId 班次id body false string
 * @param maxRemainNum 剩余名额区间end body false integer(int32)
 * @param minRemainNum 剩余名额区间start body false integer(int32)
 * @param pageNo 页码 body false integer(int32)
 * @param pageSize 每页容量【分页大小】 body false integer(int32)
 * @param serviceCenterId 服务中心id body false string
 * @param startDate 开课日期start body false string
 * @param subjectIds 学科id body false string
 * @param teacherIds 老师id body false string
 * @param termId 学期id body false string
 * @param tutorId 辅导老师id body false string
 * @param venueId 教学点id body false string
 * @param year 年份 body false string
 */
export const getClassInfo = (params) => {
  return axios.request({
    url: 'clazz/getClassInfo',
    data: params,
    method: 'post'
  })
}

