<template>
  <div class="info">
    <el-form ref="form" label-width="80px">
      <el-form-item label="昵称：">
        <el-input v-model="nickname" size="medium" :maxlength="10"  style="width: 200px"></el-input>
      </el-form-item>
      <el-form-item label="头像：">
        <el-upload
          class="avatar-uploader"
          action="#"
          :auto-upload="true"
          :show-file-list="false"
          :http-request="httpRequest"
          :on-success="handleAvatarSuccess"
          :before-upload="beforeAvatarUpload">
          <img v-show="avatarUrl" :src="avatarUrl" class="avatar">
          <i v-show='!avatarUrl' class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
        <i class="el-icon-upload"></i>
        <span>点击更换头像</span>
      </el-form-item>
    </el-form>
    <div class="footer">
      <el-button size="medium" type="primary" @click="save">保存</el-button>
    </div>
  </div>
</template>

<script>
import { NIM } from '@/common/commonData'
export default {
  data () {
    return {
      nickname: '',
      avatarUrl: ''
    }
  },
  methods: {
    handleRemove (file) {
      console.log(file)
    },
    handleAvatarSuccess (res, file) {
    },
    httpRequest (data) {
      console.log(data)
      NIM.previewFile({
        type: 'image',
        blob: data.file,
        uploadprogress: obj => {
          console.log('文件总大小: ' + obj.total + 'bytes')
          console.log('已经上传的大小: ' + obj.loaded + 'bytes')
          console.log('上传进度: ' + obj.percentage)
          console.log('上传进度文本: ' + obj.percentageText)
        },
        done: (err, data) => {
          if (err) {
            console.error(err)
          } else {
            console.log(data)
            this.avatarUrl = data.url
          }
        }
      })
    },
    beforeAvatarUpload (file) {
      console.log(file.type)
      const isJPG = file.type === 'image/jpeg' || file.type === 'image/png' || file.type === 'image/jpg'
      const isLt2M = file.size / 1024 / 1024 < 5

      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG/JPEG/PNG 格式!')
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 5MB!')
      }
      return isJPG && isLt2M
    },
    save () {
      NIM.updateMyInfo({
        // nick: this.nickname,
        avatar: this.avatarUrl,
        custom: JSON.stringify({
          nickname: this.nickname,
          avatar: this.avatarUrl
        }),
        done: (err, data) => {
          if (err) {
            console.error(err)
          } else {
            this.$store.dispatch('imchat/setUser', data)
            this.$message.success('更新成功')
            this.$emit('success')
          }
        }
      })
    }
  },
  computed: {
    user () {
      return this.$store.state.imchat.user
    }
  },
  mounted () {
    const user = this.$store.state.imchat.user
    const nickname = user.custom ? JSON.parse(user.custom).nickname : ''
    this.nickname = nickname || user.nick
    this.avatarUrl = user.avatar
  }
}
</script>

