<template>
  <div class="im-chat">
    <ChatTitle
      v-if="sessionUserInfo && sessionUserInfo.netEaseId"
      class="im-chat-title im-chat-title-oc"
      :class="{'im-chat-title-oc': crmRoleId === 8}"
      @goCustomerDetail="goCustomerDetail"
      @diagnosisHandle="diagnosisHandle"></ChatTitle>
    <div v-else>请选择聊天对象</div>
    <div
      ref="imChatBox"
      class="im-chat-box">
      <chat-list
        :is-session="!!sessionUserInfo.netEaseId"
        :can-load-more="canLoadMore"
        :msglist="msglist"
        :scroll-down.sync="scrollDown"
        @msgs-loaded="msgsLoaded"
        @editInfo="$emit('editInfo')"
        @load-more="loadMore"/>
    </div>
    <chat-editor
      v-if='sessionUserInfo.consultLabel !== 1 && sessionUserInfo.consultLabel !== 4'
      class='im-chat-edit-box'
      :can-input="!!sessionUserInfo.netEaseId"
      :to="to"
      :scene="scene"
      @endTheService='endTheServiceHandle'/>
    <div v-if="sessionUserInfo.consultLabel === 4" style="text-align: center">
      <Button @click="applyContact" type="primary">发起沟通</Button>
    </div>
  </div>
</template>

<script>
import ChatTitle from './ChatTitle'
import ChatList from './ChatList'
import ChatEditor from './ChatEditor'
import ChatOrder from './ChatOrder'
import util from '../../common/util'
import Vue from 'vue'
import { NIM } from '../../common/commonData'
import imLogger from '../../common/logger/imLogger.js'
import CustomerMixin from '../mixin/customerDetail'
import { applyStudentConsult } from '@/api/advisor'
export default {
  mixins: [ CustomerMixin ],
  components: {
    ChatTitle,
    ChatList,
    ChatEditor,
    ChatOrder
  },
  data () {
    return {
      scrollDown: true,
      dialogVisible: false
    }
  },
  computed: {
    crmRoleId () {
      return this.$store.state.roleRelativeData.userInfo.crmRoleId
    },
    msglist () {
      let msgs = this.$store.state.imchat.currSessionMsgs
      // console.log(msgs)
      return msgs
    },
    scene () {
      let session = util.parseSession(this.sessionId)
      return session ? session.scene : ''
    },
    to () {
      let session = util.parseSession(this.sessionId)
      return session ? session.to : ''
    },
    canLoadMore () {
      return !this.$store.state.imchat.noMoreHistoryMsgs
    },
    sessionId () {
      return this.$store.state.imchat.currSessionId
    },
    agent () {
      return this.$store.state.imchat.agent
    },
    sessionUserInfo () {
      let sessionId = this.sessionId
      let user = null
      if (/^p2p-/.test(sessionId)) {
        user = sessionId.replace(/^p2p-/, '')
        let userInfo = this.userInfos[user] || {}
        // this.$emit('sessionUserInfo', userInfo)
        return userInfo
      }
      return ''
    },
    userInfos () {
      return this.$store.state.imchat.userInfos
    },
    userInfo () {
      let userInfo = this.userInfos[this.to]
      let { stu_loginname, stu_mobile } = userInfo
      return { stu_loginname, stu_mobile }
    }
  },
  watch: {
    sessionId (newVal, oldVal) {
      if (newVal && newVal !== oldVal) {
        // this.getHistoryMsgs();
        const store = this.$store
        store.commit('imchat/resetNoMoreHistoryMsgs')
        // 此处必须使用getLocalMsgs，因为当开多标签且有未读消息时
        // 只有在本地的IndexedDB中取才能知道有未读的未读消息，而使用getHistoryMsgs从服务器获取不到这些未读消息，导致切换标签页后
        // 虽然有提醒未读消息，但打开之后却看不到
        let scene = this.scene
        let to = this.to
        const roleInfo = store.state.roleRelativeData.roleInfo
        const userInfos = store.state.imchat.userInfos
        let sessionId = to

        NIM.getLocalMsgs({
          sessionId: newVal,
          limit: 36,
          done: function getLocalMsgsDone (error, obj) {
            if (!error) {
              store.commit('imchat/updateMsgs', obj.msgs)
            } else {
              imLogger({
                action: 'nim_getlocalmsgs',
                from: 'PC',
                teacher_id: roleInfo.nimAccid,
                teacher_name: roleInfo.userName,
                teacher_workcode: roleInfo.userWorkCode,
                act_time_connect: new Date().getTime(),
                error: JSON.stringify(error),
                stu_name: userInfos[sessionId]
                  ? userInfos[sessionId].stuRealName
                  : ''
              })
            }
            // 这块逻辑需要优化
            // 鉴于有些老师反馈信息丢失的问题，目前感觉可能是在其他电脑发的消息未同步到其他电脑造成的
            // 所以在这里又加入了从云信拉去历史记录的操作，尽量避免该类问题的出现
            NIM.getHistoryMsgs({
              scene,
              to,
              limit: 36,
              done: function getHistoryMsgsDone (error, obj) {
                if (!error) {
                  store.commit('imchat/updateMsgs', obj.msgs)
                } else {
                  imLogger({
                    action: 'nim_gethistorymsgs',
                    from: 'PC',
                    teacher_id: roleInfo.nimAccid,
                    teacher_name: roleInfo.userName,
                    teacher_workcode: roleInfo.userWorkCode,
                    act_time_connect: new Date().getTime(),
                    error: JSON.stringify(error),
                    stu_name: userInfos[sessionId]
                      ? userInfos[sessionId].stuRealName
                      : ''
                  })
                }
              }
            })
          }
        })
      }
    }
  },
  mounted () {
    this.scrollChatListDown()
    // console.log(this.userInfos[this.sessionUserInfo.netEaseId].consultLabel !== 1)
  },
  methods: {
    applyContact () {
      applyStudentConsult({
        cityCode: this.sessionUserInfo.areaCode,
        uid: this.sessionUserInfo.pyUid
      }).then(() => {
        this.$store.dispatch('imchat/reLoadSession')
      }).catch(err => {
        this.$Message.error('发起沟通失败：' + err.message)
      })
    },
    clickInfoEv (info) {
      this.$router.push({
        name: 'stuArchives',
        params: {
          id: info.uid
        }
      })
    },
    msgsLoaded () {
      if (this.scrollDown) {
        this.scrollChatListDown()
      } else {
        this.scrollDown = true
      }
    },
    scrollChatListDown () {
      Vue.nextTick(() => {
        var chatBox = this.$refs['imChatBox']
        chatBox.scrollTop = chatBox.scrollHeight
      })
    },
    loadMore () {
      this.getHistoryMsgs()
    },
    getHistoryMsgs () {
      if (this.canLoadMore) {
        this.$store.dispatch('imchat/getHistoryMsgs', {
          scene: this.scene,
          to: this.to
        })
      }
    },
    diagnosisHandle (list) {
      try {
        let taskList = []
        list.forEach(obj => {
          taskList.push(obj.taskId)
        })
        this.diagnosisData = []
        this.$nextTick(async () => {
          const rlt = await queryStudentDiagnosis(taskList)
          this.diagnosisData = Object.freeze(rlt)
          this.diagnosisModal = true
        })
      } catch (err) {
        this.$Message.error({
          content: '获取诊断报告失败：' + err.message
        })
      } finally {
      }
    },
    endTheServiceHandle (type) {
      this.$emit('endTheService', this.sessionUserInfo)
    }
  }
}
</script>

