  display: flex;
  flex-direction: column;
  height: 100%;
  .im-chat-title {
    // flex: 0 1 45px;
    &.im-chat-title-oc {
      // flex: 0 1 120px;
      line-height: 16px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 12px;
    }
  }
  .im-chat-box {
    flex: 1;
  }
  .im-chat-edit-box {
    flex: 0 1 113px;
  }
}

.im-chat-box {
  height: 240px;
  overflow: auto;
  padding: 0 10px;
}

ul {
  padding: 0;
}

.chat-icon {
  float: right;
}

.close-btn {
  margin-right: 8px;
