<template>
  <div class="call-wrapper" :class="studentInfo.diagnosisContent ? 'h270': 'h200'">
    <div class="header" id="call-wrapper-handle">
      语音通话请求
      <Tag v-if="userInfos[studentInfo.nimAccid].consultLabel === 2" color="magenta" style="margin-left: 10px">调度任务</Tag>
      <Tag v-if="userInfos[studentInfo.nimAccid].consultLabel === 5" color="magenta" style="margin-left: 10px">代班</Tag></div>
    <div class="content">
      <p class='fontBold'>
        <span>
          {{ studentInfo.stuRealName || studentInfo.nick }}
        </span>
        <span>| {{ studentInfo.readStatusName || (studentInfo.studentJson && studentInfo.studentJson.readStatusName) || '无'  }} | {{ studentInfo.stuGradeName }} | {{ studentInfo.stuLoginName }} </span>
        <span>{{studentInfo.stuSex ? '男' : '女'}}</span>
      </p>
      <p>注册城市：{{(studentInfo.studentJson && studentInfo.studentJson.cityName) || '无'}}</p>
      <p class='fontBold'>归属城市：{{studentInfo.studentJson&&studentInfo.studentJson.belongCityName || '无'}}</p>
      <p class='fontBold'>归属服务中心：{{studentInfo.studentJson&&studentInfo.studentJson.serviceCenterName || '无'}}</p>
      <p class='fontBold'>绑定人：{{studentInfo.studentJson&&studentInfo.studentJson.consultant || '无'}}</p>
      <p class='fontBold' v-if='studentInfo.title'>标题：{{studentInfo.title}}</p>
      <template v-if='studentInfo.diagnosisContent'>
        <span class='fontBold'>诊断报告：<a v-if='(studentInfo.diagnosisContent && studentInfo.diagnosisContent.diagnosisUrl)' :href='(studentInfo.diagnosisContent && studentInfo.diagnosisContent.diagnosisUrl)' target='_blank'>{{studentInfo.studentJson.cityName }}{{ studentInfo.diagnosisContent&&studentInfo.diagnosisContent.gradeName}}诊断</a></span>
        <span>诊断年级：{{studentInfo.diagnosisContent&&studentInfo.diagnosisContent.gradeName}}</span>
        <span>科目：{{(studentInfo.diagnosisContent&&studentInfo.diagnosisContent.subjectName)}}</span>
        <span>分数：{{(studentInfo.diagnosisContent&&studentInfo.diagnosisContent.score)}}</span>
        <span>班次级别：{{(studentInfo.diagnosisContent&&studentInfo.diagnosisContent.levelDegreeName)}}</span>
        <span>班次名称：{{(studentInfo.diagnosisContent&&studentInfo.diagnosisContent.levelName)}}</span>
        <span>成绩来源：{{(studentInfo.diagnosisContent&&studentInfo.diagnosisContent.sourceTypeName)}}</span>
        <span>成绩是否有效：有效</span>
        <span>作答时间：{{(studentInfo.diagnosisContent&&studentInfo.diagnosisContent.answerTime)|dateFilter}}</span>
      </template>
    </div>
    <div class="footer">
      <Button type="error" @click="reject">拒绝</Button>
      <Button type="success" @click="accept">接听</Button>
    </div>
  </div>
</template>

<script>
import { dateFormat } from '@/utils/utils'
import util from '@/common/util'
export default {
  name: 'BeCall',
  filters: {
    dateFilter (val) {
      return dateFormat(val)
    }
  },
  props: {
    account: String
  },
  data () {
    return {
    }
  },
  computed: {
    studentInfo () {
      return this.$store.state.imchat.crtCallUser
    },
    userInfos () {
      return this.$store.state.imchat.userInfos
    }
  },
  methods: {
    accept () {
      const browserName = util.browser()
      if (browserName.includes('Chrome')) {
        this.$store.dispatch('imchat/accept')
      } else {
        this.$Message.warning({
          content: '为保证语音功能正常使用，请使用谷歌浏览器',
          duration: 5
        })
      }
    },
    reject () {
      this.$store.dispatch('imchat/reject')
    }
  }
}
</script>

<style scoped lang="less">
  .call-wrapper {
    background: #eee;
    padding: 10px;
    border-radius: 4px;
    border: 1px solid #000000;
    width: 400px;
    z-index: 10000;
    .header {
      cursor: move;
      font-size: 16px;
      font-weight: bold;
      padding-bottom: 10px;
      margin-bottom: 10px;
      border-bottom: 1px solid #ccc
    }
    .footer {
      text-align: right;
    }
  }
  .h270 {
    height: 270px;
  }
  .h200 {
    height: 200px;
  }
</style>
