import axios from '@/libs/api.request'

/**
 *  新增反馈
 * @param params
 * @returns {*}
 */
export function addFeedback (params) {
  return axios.request({
    url: 'feedback/addFeedback',
    method: 'post',
    data: params
  })
}

/**
 *  反馈信息列表
 * @param params
 * @returns {*}
 */
export function queryFeedbackPage (params) {
  return axios.request({
    url: 'feedback/queryFeedbackPage',
    method: 'post',
    data: params
  })
}

/**
 *  处理反馈
 * @param params
 * @returns {*}
 */
export function handleFeedback (params) {
  return axios.request({
    url: 'feedback/handleFeedback',
    method: 'post',
    data: params
  })
}

/**
 *  查询反馈的操作日志
 * @param params
 * @returns {*}
 */
export function queryLogListByFeedbackId (params) {
  return axios.request({
    url: 'feedback/queryLogListByFeedbackId',
    method: 'get',
    params: params
  })
}
