<template>
  <div class="chat-order">
    <section class='communicate-results'>
      <header>学员标签</header>
      <section>
        <el-checkbox-group v-model="tags" size="small">
          <el-checkbox-button label="报班意向强" border></el-checkbox-button>
          <el-checkbox-button label="诊断已解析" border ></el-checkbox-button>
        </el-checkbox-group>
      </section>
      <header><span>* </span>沟通结果：</header>
      <section>
        <el-input
          v-model='contactResult'
          maxlength="500"
          :rows='4'
          show-word-limit
          type="textarea"
          placeholder="沟通结果" />
      </section>
      <footer>
        <Button class='submit-btn' type='primary' :loading='loading' @click='commitWorkOrderHandle'>提交</Button>
      </footer>
    </section>
  </div>
</template>

<script>
import { commitWorkOrder } from '../../api/advisor'
import ChatTitle from './ChatTitle'
export default {
  components: {
    ChatTitle
  },
  props: {
    sessionUserInfo: {
      type: [Object, String]
    },
    inTheEnd: Boolean
  },
  computed: {
    currImTitleInfo () {
      return this.$store.state.imchat.currImTitleInfo
    },
    userInfo () {
      return this.$store.state.user.userInfo
    }
  },
  watch: {
    currImTitleInfo (old, _new) {
      if (old.id !== _new.id) {
        this.resetField()
      }
    }
  },
  data () {
    return {
      tags: [],
      contactResult: '',
      loading: false
    }
  },
  methods: {
    resetField () {
      this.tags = []
      this.contactResult = ''
    },
    goCustomerDetail () {
      this.$emit('goCustomerDetail', this.sessionUserInfo)
    },
    diagnosisHandle () {
      this.$emit('diagnosisHandle', this.sessionUserInfo)
    },
    commitWorkOrderHandle () {
      if (!this.contactResult) {
        this.$message({
          message: '沟通结果为必填项',
          type: 'error'
        })
        return
      }
      // let foreignId = ''
      // this.$store.state.imchat.currSessionMsgs.forEach(obj => {
      //   // console.log(obj)
      //   if (obj.type === 'custom') {
      //     if (obj.custom) {
      //       foreignId = (JSON.parse(obj.custom)).foreignId
      //     }
      //   }
      // })
      let params = {
        consultId: this.sessionUserInfo.id,
        commitStatus: this.inTheEnd,
        pyUid: this.sessionUserInfo.pyUid,
        personalityTagList: this.tags,
        contactResult: this.contactResult,
        empName: this.userInfo.name,
        isActive: true
      }
      // console.log(this.showUserInfo, this.sessionUserInfo.pyUid, this.tags)
      if (this.inTheEnd) {
        params['studentJson'] = JSON.stringify(Object.assign({
          consultant: this.sessionUserInfo.consultant,
          title: this.sessionUserInfo.title
        }, this.sessionUserInfo.studentJson, this.sessionUserInfo.diagnosisContent))
      }
      this.loading = true
      commitWorkOrder(params).then(res => {
        this.$message({
          message: '提交成功',
          type: 'success'
        })
        this.resetField()
        if (this.inTheEnd) {
          this.$emit('close')
          this.$store.dispatch('imchat/reLoadSession')
        } else {
          this.$emit('refresh')
        }
      }).catch(err => {
        this.$message({
          message: err.message,
          type: 'error'
        })
      }).finally(() => {
        this.loading = false
      })
    }
  }
}
</script>

<style lang="less" scoped>
.chat-order {
  display: flex;
  flex-direction: column;
  & > section {
    flex: 1;
    &.communicate-results {
      flex: 0 1 140px;
      header,
      footer {
        padding: 5px 0;
        font-size: 12px;
      }
      header {
        & > span {
          color: red;
        }
      }
    }
  }
}
.submit-btn {
  float: right;
}
</style>
