import axios from '@/libs/api.request'
import auth from '@/libs/auth.request'
export const getRouterReq = (access) => {
  return axios.request({
    url: 'get_router',
    params: {
      access
    },
    method: 'get'
  })
}
// 一站式权限
export const getRoleResources = params => {
  return auth.request({
    url: '/auth/getUserResource',
    method: 'get',
    params: params
  })
}
export const getSource = (params) => {
  return auth.request({
    url: 'resource/list',
    method: 'POST',
    data: params
  })
}

// 获取用户左侧菜单
export const getResourcesByEmpNo = params => {
  return auth.request({
    method: 'get',
    url: 'resource/getResourcesByEmpNo',
    params
  })
}
