export default {
  // 金额保存两位小数点
  formatMoneyFilter (val) {
    const valList = [null, undefined, 'null', 'undefined']
    if (valList.includes(val)) {
      return '--'
    }
    // const valArr = Number(val).toFixed(2).toString().split('.');
    // const reg = /\d{1,3}(?=(\d{3})+$)/g;
    // const firstVal = valArr[0].replace(reg, '$&,');
    // return `${firstVal}.${valArr[1]}`;
    return Number(val).toFixed(2)
  },
  /**
   * 转换为3位计数法
   * @param num
   * @returns {string}
   */
  toThousands (num) {
    return (num || 0).toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,')
  }
}
