<template>
  <div id="app" :class="{overflow: isTop}">
    <router-view></router-view>
    <Modal title="选择角色" v-model="showCheckRoleModal" footer-hide :mask-closable="false" :closable="false">
      <Row type="flex" justify="center">
        <Col>
          <span>角色 &nbsp;&nbsp;&nbsp;</span>
          <Select v-model="checkedIdentityId" style="width:300px">
            <Option
              v-for="item in roleList"
              :value="item.identityId"
              :key="item.identityId"
            >{{item.deptName}}-{{ item.roleName }}</Option>
          </Select>
        </Col>
        <Col class="margin-top">
          <p class="tips">提示: 收款时款项会计入当前角色所属服务中心，同时关联结报帐</p>
        </Col>
        <Col class="margin-top">
          <Button size="large" class="confirm" type="primary" @click="confirm">确认</Button>
        </Col>
      </Row>
    </Modal>
  </div>
</template>

<script>
import { registerLastIdentity } from '@/api/crm'
import Cookie from 'js-cookie'
import { initTrack } from '@/libs/tal-sensors'
export default {
  name: 'App',
  data () {
    return {
      isRouterAlive: true,
      isTop: top === window,
      showCheckRoleModal: false,
      checkedIdentityId: '',
      roleList: []
    }
  },
  computed: {
    isMini () {
      return this.$store.state.imchat.isShowMini
    }
  },
  watch: {
    '$store.state.roleRelativeData.allowButtonList' () {
      this.$store.dispatch('initWxInterval', 'first')
    },
    '$route' (val, oldVal) { // 离开补报页面启动轮询检查是否补报
      if (oldVal.name === 'prescribeddaily') {
        this.$store.dispatch('resetCheckMissReport', { modal: this.$Modal })
      }
    }
  },
  async mounted () {
    await this.initRole()
    initTrack(this)
  },
  methods: {
    confirm () {
      if (this.checkedIdentityId !== this.latestRole.authIdentityId) {
        this.changeRoleHandler(this.checkedIdentityId)
      } else {
        this.$store.dispatch('resetCheckMissReport', { modal: this.$Modal, first: true })
      }
      this.showCheckRoleModal = false
    },
    // 从一站式跳转
    jumpFromOnestop (authIdentityId) {
      // let currentRole = this.roleList.filter(obj => authIdentityId === obj.identityId)[0]
      // // 从一站式子菜单跳转到盘古，可能对应的身份已经不存在
      // if (!currentRole) {
      //   this.$Message.error({
      //     content: '当前身份已不存在',
      //     duration: 5
      //   })

      //   window.location.href = '/service/home'
      //   return
      // }

      // getResourcesByEmpNo({ type: 0, roleId: currentRole.roleId }).then(resp => {
      //   if (resp) {
      //     let hasMenu = false
      //     const resourceList = resp.children
      //     console.log('role resoource', resourceList)
      //   }
      // }).catch(err => {
      //   this.$Message.error({
      //     content: '获取菜单权限失败: ' + err.message,
      //     duration: 5
      //   })
      // })
      this.changeRoleHandler(authIdentityId, true)
    },
    initRole () {
      return this.$store.dispatch('roleRelativeData/gainRoleList').then(res => {
        if (res) {
          const { roleList, latestRole } = res
          this.roleList = roleList
          this.latestRole = latestRole
          this.checkedIdentityId = latestRole.authIdentityId
          const { from, authIdentityId, print } = this.$route.query
          const routeName = this.$route.name
          if (this.$store.state.roleRelativeData.roleIds.includes(latestRole.crmRoleId)) {
            if (!print) { // 打印页面不能进行跳转
              // this.$router.push({ name: 'adviserDataKanban' })
              // setTimeout(() => {
              //   this.$router.push({ name: 'advisorWorkbench' })
              // }, 500)
            }
            const callGrayCity = this.$store.state.app.callGrayCity
            if (!callGrayCity || !callGrayCity.length || callGrayCity.includes(latestRole.cityCode)) { // 外呼组件是否灰度显示
              this.$store.dispatch('initCallOutSideConfig')
            }
          }
          // 一站式路由跳转
          if (authIdentityId && latestRole.authIdentityId !== authIdentityId) { // authIdentityId=5cdf599cc4bc11e99d7a7cd30ac4cda2
            this.jumpFromOnestop(authIdentityId)
            return
          }
          // 只在首页校验、且通过点击一站式tab时触发
          if (from !== 'switchRole' && routeName === 'home' && this.roleList.length > 1) {
            this.showCheckRoleModal = true
          } else {
            this.$store.dispatch('resetCheckMissReport', { modal: this.$Modal, first: true })
          }
        }
      })
    },
    async changeRoleHandler (role, refreshOnly) {
      let currentRole = this.roleList.filter(obj => role === obj.identityId)[0]
      // 从一站式子菜单跳转到盘古，可能对应的身份已经不存在
      // if (!currentRole) {
      //   this.$Message.error({
      //     content: '当前身份已不存在',
      //     duration: 5
      //   })

      //   window.location.href = '/service/home'
      //   return
      // }
      let req = {
        empNo: JSON.parse(Cookie.get('columbusUserInfo')).id,
        empName: decodeURI(JSON.parse(Cookie.get('columbusUserInfo')).name),
        ...currentRole
      }
      try {
        const data = await registerLastIdentity(req)
        await this.$store.dispatch('resetCheckMissReport', { modal: this.$Modal, first: true })
        if (data) {
          localStorage['authIdentityId'] = role
          // 自动切换角色，刷新当前路由，不返回首页
          if (refreshOnly) {
            window.location.href = window.location.origin + window.location.pathname
            return
          }
          // 手动切换角色返回首页
          window.location.href = `/service/home?from=switchRole`
        } else {
          this.$Message.error({
            content: '切换角色失败'
          })
        }
      } catch (err) {
        this.$Message.error({
          content: '切换角色失败：' + err.message
        })
      }
    }
  },
  beforeDestroy () {
    if (this.$store.state.app.wxInterval) {
      clearInterval(this.$store.state.app.wxInterval)
    }
  }
}
</script>

