import { NIM, NimWebRTC } from '@/common/commonData'
import Vue from 'vue'
import { getDurationText } from '@/utils/utils'
import { syncAviContact } from '@/api/advisor'
const connecting = require('../../../../public/audio/avchat_connecting.mp3')
const noResponse = require('../../../../public/audio/avchat_no_response.mp3')
const peerBusy = require('../../../../public/audio/avchat_peer_busy.mp3')
const peerReject = require('../../../../public/audio/avchat_peer_reject.mp3')
const ring = require('../../../../public/audio/avchat_ring.mp3')

export default {
  initWebRTCEvent () {
    this.xesTrack = XesAnalytics
    this.nim = NIM
    this.webrtc = NimWebRTC
    this.vue = Vue.prototype
    this.store = Vue.prototype.$store
    // 呼叫是否被接受或拒绝
    this.callAcceptedOrRejected = false
    // 呼叫超时检查定时器
    this.callTimer = null
    // 被呼叫超时检查定时器
    this.beCallTimer = null
    // 通话时长定时器
    this.netcallDurationTimer = null
    // 通话的channelId
    this.channelId = null
    // 通话流程的另一方账户
    this.netcallAccount = ''
    // 通话时长
    this.netcallDuration = 0
    // 通话正式开始时间戳
    this.netcallStartTime = 0
    // 通话正式结束时间戳
    this.netcallEndTime = 0
    this.callType = null
    // 是否正在挂断中
    this.hunguping = false
    // 音视频流配置
    this.sessionConfig = {
      videoQuality: WebRTC.CHAT_VIDEO_QUALITY_480P,
      videoFrameRate: WebRTC.CHAT_VIDEO_FRAME_RATE_NORMAL,
      videoBitrate: 0,
      recordVideo: false,
      recordAudio: false,
      highAudio: false
    }
    // 对方接受通话 或者 我方接受通话，都会触发
    this.webrtc.on('callAccepted', function (obj) {
      this.onCallAccepted(obj)
    }.bind(this))
    this.webrtc.on('callRejected', function (obj) {
      this.onCallingRejected(obj)
    }.bind(this))
    this.webrtc.on('signalClosed', function () {
      console.log('signal closed')
      this.beCalling = false
      this.beCalledInfo = null
      this.webrtc.hangup()
      this.clearRingPlay()
      this.resetWhenHangup()
    }.bind(this))
    this.webrtc.on('rtcConnectFailed', function () {
      console.log('rtcConnectFailed')
      console.log('rtc 连接中断')
      this.beCalling = false
      this.beCalledInfo = null
      this.webrtc.hangup()
      this.clearRingPlay()
      this.resetWhenHangup()
    }.bind(this))
    this.webrtc.on('devices', function (obj) {
      console.log('on devices:', obj)
    })
    this.webrtc.on('deviceStatus', function (obj) {
      console.log('on deviceStatus:', obj)
    })
    this.webrtc.on('deviceAdd', function (obj) {
      console.log('on deviceAdd', obj)
    })
    this.webrtc.on('deviceRemove', function (obj) {
      console.log('on deviceRemove', obj)
    })
    this.webrtc.on('beCalling', function (obj) {
      this.onBeCalling(obj)
    }.bind(this))
    this.webrtc.on('control', function (obj) {
      this.onControl(obj)
    }.bind(this))
    this.webrtc.on('hangup', obj => {
      console.log('收到对方挂断通话消息', obj, `this.channelId = ${this.channelId}`)
      if (this.hunguping) { // 因为语音接通后挂断会连续收到两次挂断消息，所以需要判断当前是否正在挂断中
        this.hunguping = false
      } else {
        this.onHangup(obj)
        this.hunguping = true
      }
    })
    this.webrtc.on('heartBeatError', function (obj) {
      console.log('heartBeatError,要重建信令啦')
    })
    this.webrtc.on('callerAckSync', obj => {
      console.log(obj)
      this.onCallerAckSync(obj)
    })
    this.webrtc.on('netStatus', function (obj) {
      console.log('on net status:', obj)
      switch (obj.quality) {
        case WebRTC.CHAT_NET_STATUS_VERY_GOOD:
          console.log('当前网络状况非常好')
          break
        case WebRTC.CHAT_NET_STATUS_GOOD:
          console.log('网络状况好，音视频通话偶有卡顿')
          break
        case WebRTC.CHAT_NET_STATUS_POOR:
          console.log('网络状况差，音频通话流畅')
          break
        case WebRTC.CHAT_NET_STATUS_BAD:
          this.vue.$Message.warning({
            content: '网络状况不佳',
            duration: 5
          })
          console.log('网络状况非常差，音频通话偶有卡顿')
          break
        case WebRTC.CHAT_NET_STATUS_VIDEO_CLOSE:
          console.log('WebRTC.HAT_NET_STATUS_VIDEO_CLOSE')
          break
        default:
          console.log('未知网络状态')
      }
    })
    this.webrtc.on('statistics', function (obj) {
      console.log('on statistics:', obj)
    })
    this.webrtc.on('audioVolume', obj => {
      console.log('audioVolume', obj)
    })
    this.webrtc.on('joinChannel', function (obj) {
      // if (this.callMethod !== 'webrtc' || window.yunXin.WB.session.length !== 0) return
      console.log('user join', obj)
      // that.onJoinChannel(obj);
    })
    this.webrtc.on('remoteTrack', obj => {
      console.log('收到远程轨道信息：', obj)
      if (obj.track && obj.track.kind === 'audio') {
        this.setDeviceAudioOut(true)
      }
      if (obj.track && obj.track.kind === 'video' && this.callType === WebRTC.NETCALL_TYPE_VIDEO) {
        this.startRemoteStream(obj)
      }
    })
    this.webrtc.on('leaveChannel', function (obj) {
      // if (this.callMethod !== 'webrtc' || window.yunXin.WB.session.length !== 0) return
      console.log('sb leaveChannel', obj)
    })
    this.webrtc.on('userStateUpdated', obj => {
      console.log('userStateUpdated', obj)
    })
  },
  videoType: null,
  onCallAccepted (obj) {
    console.log(this.beCalledInfo)
    this.channelId = obj.channelId
    this.callType = obj.type
    console.log('收到接听事件：', obj, this.channelId)
    let that = this
    Promise.resolve().then(() => {
      console.log('开始webrtc连接')
      return this.webrtc.startRtc()
    }).then(() => {
      console.log('webrtc连接成功')
      return that.setDeviceAudioIn(true)
    }).then(() => {
      that.clearRingPlay()
      this.store.commit('imchat/setCallOther', false)
      this.store.commit('imchat/setCalling', true)
      return this.startDurationTimer()
    }).then(() => {
      this.callAcceptedOrRejected = true
      this.acceptAndWait = false
      this.clearBeCallTimer()
      this.clearCallTimer()
      console.log('音视频通话开始')
      syncAviContact({
        consultId: this.store.state.imchat.crtCallUser.id,
        stuAccid: this.store.state.imchat.crtCallUser.nimAccid,
        nimAccid: this.store.state.roleRelativeData.roleInfo.nimAccid,
        launchType: '1',
        id: obj.channelId,
        pyUid: this.store.state.imchat.userInfos[this.netcallAccount].pyUid,
        fromAccId: this.beCalledInfo ? this.store.state.imchat.crtCallUser.nimAccid : this.store.state.roleRelativeData.roleInfo.nimAccid
      })
    }).catch(e => {
      console.error(e)
      this.onHangup(obj)
      this.vue.$Message.error('连接出错')
      console.log('连接出错')
    })
  },
  async onHangup (obj) {
    try {
      if (obj && obj.account) {
        if (this.channelId) {
          if (obj.channelId !== this.channelId) {
            console.log('收到非当前通话挂断消息', obj.channelId, this.channelId)
            this.hunguping = false
            return
          }
        } else { // 如果channelId为 null，延迟1s判断是否为相同的channelId
          const same = await new Promise(resolve => {
            setTimeout(() => {
              resolve(obj.channelId === this.channelId)
            }, 1000)
          })
          if (!same) {
            console.log('收到非当前通话挂断消息', obj.channelId, this.channelId)
            this.hunguping = false
            return
          }
        }
        // this.store.commit('imchat/setShowMini', false)
        // this.store.dispatch('imchat/acceptCallToSession', obj.account)
        if (obj.account === this.netcallAccount) {
          if (obj.type === -1) {
            this.vue.$Message.error({
              content: '对方出现异常，已挂断',
              duration: 5
            })
          }
        } else {
          if (obj.type === -1) {
            this.vue.$Message.error({
              content: '我方出现异常，已挂断',
              duration: 5
            })
          }
        }
      }
      // '用户打给顾问-成功接通'/'顾问打给用户-成功接通'
      this.xesTrack.track(this.beCalledInfo ? '15-1519' : '15-1514', {
        consult_id: this.store.state.imchat.crtCallUser.id,
        adviser: this.store.state.roleRelativeData.userInfo.crmRoleId,
        adviser_departmentID: this.store.state.roleRelativeData.userInfo.crmDeptId,
        adviser_departmentNAME: '',
        adviser_empNo: this.store.state.roleRelativeData.roleInfo.userWorkCode,
        adviser_citycode: this.store.state.roleRelativeData.userInfo.cityCode,
        start_time: this.netcallStartTime,
        end_time: this.netcallEndTime
      })
      const consultLabel = this.store.state.imchat.userInfos[this.netcallAccount].consultLabel
      if (obj) { // 被动挂断，延时2s挂断
        setTimeout(() => {
          this.webrtc.hangup()
          this.clearRingPlay()
          syncAviContact({
            consultId: this.store.state.imchat.crtCallUser.id,
            stuAccid: this.store.state.imchat.crtCallUser.nimAccid,
            nimAccid: this.store.state.roleRelativeData.roleInfo.nimAccid,
            launchType: '2',
            id: this.channelId,
            pyUid: this.store.state.imchat.userInfos[this.netcallAccount].pyUid,
            fromAccId: this.beCalledInfo ? this.store.state.imchat.crtCallUser.nimAccid : this.store.state.roleRelativeData.roleInfo.nimAccid
          })
          if (consultLabel === 2) {
            this.vue.$Message.warning({
              content: '调度任务请尽快点击结束服务提交工单，系统会在5分钟后自动提交工单',
              duration: 5
            })
          }
          this.resetWhenHangup()
        }, 2000)
      } else { // 主动挂断
        this.webrtc.hangup()
        this.clearRingPlay()
        setTimeout(() => {
          syncAviContact({
            consultId: this.store.state.imchat.crtCallUser.id,
            stuAccid: this.store.state.imchat.crtCallUser.nimAccid,
            nimAccid: this.store.state.roleRelativeData.roleInfo.nimAccid,
            launchType: '2',
            id: this.channelId,
            pyUid: this.store.state.imchat.userInfos[this.netcallAccount].pyUid,
            fromAccId: this.beCalledInfo ? this.store.state.imchat.crtCallUser.nimAccid : this.store.state.roleRelativeData.roleInfo.nimAccid
          })
          if (consultLabel === 2) {
            this.vue.$Message.warning({
              content: '调度任务请尽快点击结束服务提交工单，系统会在5分钟后自动提交工单',
              duration: 5
            })
          }
          this.resetWhenHangup()
        }, 500)
      }
    } catch (e) {
      console.log(e)
      this.hunguping = false
      this.vue.$Message.error({
        content: '挂断电话出错',
        duration: 5
      })
    }
  },
  onCallingRejected (obj) {
    console.log('对方已拒绝音频请求', obj)
    // '顾问打给用户-用户拒接'
    this.xesTrack.track('15-1521', {
      consult_id: this.store.state.imchat.crtCallUser.id,
      adviser: this.store.state.roleRelativeData.userInfo.crmRoleId,
      adviser_departmentID: this.store.state.roleRelativeData.userInfo.crmDeptId,
      adviser_departmentNAME: '',
      adviser_empNo: this.store.state.roleRelativeData.roleInfo.userWorkCode,
      adviser_citycode: this.store.state.roleRelativeData.userInfo.cityCode
    })
    this.callAcceptedOrRejected = true
    this.clearRingPlay()
    this.playRing('D', 1)
    // this.store.dispatch('imchat/sendLocalMessage', '对方已拒绝音频请求')
    // this.webrtc.hangup()
    this.store.commit('imchat/setCallOther', false)
  },
  onBeCalling (obj, scene) {
    console.log('on be calling:', obj)
    scene = scene || 'p2p'
    // 如果是同一通呼叫，直接丢掉
    if (obj.channelId === this.channelId) return
    // 自己正在通话或者被叫中, 知对方忙并拒绝通话
    if (this.beCalling ||
      this.store.state.imchat.calling ||
      this.store.state.imchat.beCalling ||
      this.store.state.imchat.callOther) {
      // var tmp = { command: WebRTC.NETCALL_CONTROL_COMMAND_BUSY }
      // if (scene === 'p2p') {
      //   tmp.channelId = obj.channelId
      // }
      // console.log('通知呼叫方我方没空', obj.account)
      // this.webrtc.control(tmp)
      this.webrtc.response({
        accepted: false,
        beCalledInfo: obj
      }).then(function () {
        console.log('我方正忙时拒绝对方音视频通话请求成功')
      }).catch(function (err) {
        // 自己断网了
        console.log('我方正忙时拒绝对方音视频通话请求失败', err)
      })
      this.store.dispatch('imchat/showMissedCall', obj.account)
      // this.sendLocalMessage('对方发起通话请求，请注意', obj.account)
      // '用户打给顾问-顾问忙线'
      this.xesTrack.track('15-1515', {
        consult_id: this.store.state.imchat.crtCallUser.id,
        adviser: this.store.state.roleRelativeData.userInfo.crmRoleId,
        adviser_departmentID: this.store.state.roleRelativeData.userInfo.crmDeptId,
        adviser_departmentNAME: '',
        adviser_empNo: this.store.state.roleRelativeData.roleInfo.userWorkCode,
        adviser_citycode: this.store.state.roleRelativeData.userInfo.cityCode
      })
      return
    }

    // 正常发起通话请求
    this.type = obj.type
    this.netcallAccount = obj.account
    this.channelId = obj.channelId
    this.beCalling = true
    setTimeout(() => {
      this.store.dispatch('imchat/setCaller', obj.account)
    }, 500)

    // p2p场景
    this.beCalledInfo = obj
  },
  accept () {
    if (!this.beCalling) {
      console.log('当前未在被呼叫状态，操作无效')
      return
    }
    console.log('同意对方音视频请求')
    this.webrtc.response({
      accepted: true,
      beCalledInfo: this.beCalledInfo,
      sessionConfig: this.sessionConfig
    }).then(function () {
      this.beCalling = false
      console.log('同意对方音视频请求成功', this.beCalledInfo)
      // 加个定时器 处理点击接听了 实际上对面杀进程了，没有callAccepted回调
      this.clearBeCallTimer()
      this.clearRingPlay()
      this.acceptAndWait = true
      this.store.commit('imchat/setBeCalling', false)
      this.store.commit('imchat/setCalling', true)
      this.store.commit('imchat/setShowMini', false)
      this.store.dispatch('imchat/acceptCallToSession', this.beCalledInfo.account)
      // 加个定时器 处理点击接听了 实际上对面杀进程了，没有callAccepted回调
      setTimeout(function () {
        if (this.acceptAndWait) {
          console.log('通话建立过程超时')
          this.onHangup(this.beCalledInfo)
          this.acceptAndWait = false
        }
      }.bind(this), 45 * 1000)
    }.bind(this)).catch(function (err) {
      this.beCalling = false
      console.log('同意对方音视频通话失败，转为拒绝')
      console.log('error info:', err)
      this.reject()
    }.bind(this))
  },
  reject () {
    if (!this.beCalling) {
      console.log('当前未在被呼叫状态，操作无效')
      return
    }
    // 用户打给顾问-顾问拒接
    this.xesTrack.track('15-1517', {
      consult_id: this.store.state.imchat.crtCallUser.id,
      adviser: this.store.state.roleRelativeData.userInfo.crmRoleId,
      adviser_departmentID: this.store.state.roleRelativeData.userInfo.crmDeptId,
      adviser_departmentNAME: '',
      adviser_empNo: this.store.state.roleRelativeData.roleInfo.userWorkCode,
      adviser_citycode: this.store.state.roleRelativeData.userInfo.cityCode
    })
    this.clearBeCallTimer()
    this.clearRingPlay()
    console.log('拒绝对方音视频通话请求')
    this.webrtc.response({
      accepted: false,
      beCalledInfo: this.beCalledInfo
    }).then(function () {
      console.log('拒绝对方音视频通话请求成功')
      // this.store.commit('imchat/setShowMini', false)
      // this.store.dispatch('imchat/acceptCallToSession', this.beCalledInfo.account)
      this.beCalledInfo = null
      this.beCalling = false
      this.store.commit('imchat/setBeCalling', false)
    }.bind(this)).catch(function (err) {
      // 自己断网了
      console.log('拒绝对方音视频通话请求失败')
      console.log('error info:', err)
      this.beCalledInfo = null
      this.beCalling = false
      this.store.commit('imchat/setBeCalling', false)
    }.bind(this))
  },
  playRing (name, count, done) {
    done = done || function () { }
    this.playRingInstance = this.playRingInstance || {}
    const nameMap = {
      A: connecting,
      B: noResponse,
      C: peerBusy,
      D: peerReject,
      E: ring
    }
    const url = nameMap[name]
    console.log('播放提示音', url)
    const doPlay = (url, playDone) => {
      let audio = document.createElement('audio')
      audio.autoplay = true
      const onEnded = () => {
        if (this.playRingInstance) {
          this.playRingInstance.cancel = null
        }
        audio = null
        playDone()
      }
      audio.addEventListener('ended', onEnded)
      audio.src = url
      this.playRingInstance.cancel = function () {
        audio.removeEventListener('ended', onEnded)
        audio.pause()
        audio = null
      }
    }
    let wrap = () => {
      this.playRingInstance = null
      done()
    }
    for (let i = 0; i < count; i++) {
      wrap = (function (wrap) {
        return function () {
          doPlay(url, wrap)
        }
      })(wrap)
    }
    wrap()
  },
  /**
   * 开关麦克风
   * @param state
   * @returns {*}
   */
  setDeviceAudioIn (state) {
    if (state) {
      console.log('开启麦克风')
      return this.webrtc.startDevice({
        // 开启麦克风输入
        type: WebRTC.DEVICE_TYPE_AUDIO_IN
      }).then(() => {
        console.log('开启麦克风成功，通知对方我方开启了麦克风')
        // 通知对方自己开启了麦克风
        this.webrtc.control({
          command: WebRTC.NETCALL_CONTROL_COMMAND_NOTIFY_AUDIO_ON
        })
        this.webrtc.setCaptureVolume(255)
      }).catch(() => {
        this.vue.$Message.error('开启麦克风失败，请检查设备是否正常')
        // 麦克风不可用提示
        this.webrtc.control({
          command: WebRTC.NETCALL_CONTROL_COMMAND_SELF_AUDIO_INVALID
        })
      })
    } else {
      console.log('关闭麦克风')
      return this.webrtc.stopDevice(WebRTC.DEVICE_TYPE_AUDIO_IN) // 关闭麦克风输入
        .then(() => {
          console.log('关闭麦克风成功，通知对方我方关闭了麦克风')
          // 通知对方自己关闭了麦克风
          this.webrtc.control({
            command: WebRTC.NETCALL_CONTROL_COMMAND_NOTIFY_AUDIO_OFF
          })
        }).catch(err => {
          console.log('关闭麦克风失败', err)
          this.vue.$Message.error('关闭麦克风失败，请检查设备是否正常')
        })
    }
  },
  /**
   * 开关扬声器
   * @param state
   * @returns {*}
   */
  setDeviceAudioOut (state) {
    if (state) {
      console.log('开启扬声器')
      return this.webrtc.startDevice({
        type: WebRTC.DEVICE_TYPE_AUDIO_OUT_CHAT
      }).then(() => {
        const volume = this.webrtc.setPlayVolume(255)
        console.log('设置扬声器声音大小：', volume)
      }).catch(() => {
        console.error('开启扬声器失败')
        this.vue.$Message.error('扬声器不可用，请检查设备是否正常')
        // // 扬声器不可用提示
        this.webrtc.control({
          command: WebRTC.NETCALL_CONTROL_COMMAND_SELF_AUDIO_INVALID
        })
      })
    } else {
      console.log('关闭扬声器')
      return this.webrtc.stopDevice(WebRTC.DEVICE_TYPE_AUDIO_OUT_CHAT).then(() => {
        console.log('关闭扬声器成功')
      }).catch(() => {
        console.error('关闭扬声器失败')
      })
    }
  },
  cancelCalling (isClick) {
    if (isClick) {
      console.log('取消呼叫')
      this.clearRingPlay()
    }
    this.webrtc.hangup()
    setTimeout(() => {
      this.resetWhenHangup()
    }, 1000)
  },
  startDurationTimer () {
    this.clearDurationTimer()
    const timer = () => {
      this.netcallEndTime = (new Date()).getTime()
      this.netcallDuration = this.netcallEndTime - this.netcallStartTime
      let timeText = getDurationText(this.netcallDuration)
      let timeWrapper = this.store.state.imchat.callTimeWrapper
      if (timeWrapper) {
        timeWrapper.innerHTML = timeText
      }
    }
    this.netcallDuration = 0
    this.netcallStartTime = new Date().getTime()
    this.netcallDurationTimer = setInterval(timer, 500)
    timer()
  },
  clearCallTimer () {
    if (this.callTimer) {
      clearTimeout(this.callTimer)
      this.callTimer = null
    }
  },
  clearBeCallTimer () {
    if (this.beCallTimer) {
      clearTimeout(this.beCallTimer)
      this.beCallTimer = null
    }
  },
  clearRingPlay () {
    console.log('停止播放提示音')
    if (this.playRingInstance) {
      this.playRingInstance.cancel && this.playRingInstance.cancel()
      this.playRingInstance = null
    }
  },
  clearDurationTimer () {
    if (this.netcallDurationTimer) {
      clearInterval(this.netcallDurationTimer)
      this.netcallDuration = 0
      this.netcallDurationTimer = null
    }
  },
  resetWhenHangup () {
    this.store.commit('imchat/setCallOther', false)
    this.store.commit('imchat/setBeCalling', false)
    this.store.commit('imchat/setCalling', false)
    this.beCalledInfo = null
    this.beCalling = false
    this.callType = null
    // this.clearRingPlay() // 清除提示音播放
    this.clearCallTimer() // 清除主叫计时器
    this.clearBeCallTimer() // 清除被叫叫计时器
    this.clearDurationTimer() // 清除通话计时器
    this.setDeviceAudioIn(false) // 关闭麦克风
    this.setDeviceAudioOut(false) // 关闭扬声器
    this.stopRemoteStream()
    /** 放开通道 */
    this.channelId = null
    this.signalInited = false
    this.hunguping = false
    this.webrtc && this.webrtc.stopSignal && this.webrtc.stopSignal()
  },
  // 监听对方发送消息
  onControl (obj) {
    console.log('on control:', obj)
    // 如果不是当前通话的指令, 直接丢掉
    if (this.webrtc.notCurrentChannelId(obj)) {
      console.log('非当前通话的控制信息')
      return
    }

    var type = obj.type
    switch (type) {
      // NETCALL_CONTROL_COMMAND_NOTIFY_AUDIO_ON 通知对方自己打开了音频
      case WebRTC.NETCALL_CONTROL_COMMAND_NOTIFY_AUDIO_ON:
        console.log('对方打开了麦克风')
        break
      // NETCALL_CONTROL_COMMAND_NOTIFY_AUDIO_OFF 通知对方自己关闭了音频
      case WebRTC.NETCALL_CONTROL_COMMAND_NOTIFY_AUDIO_OFF:
        console.log('对方关闭了麦克风')
        break
      // NETCALL_CONTROL_COMMAND_NOTIFY_VIDEO_ON 通知对方自己打开了视频
      // case Netcall.NETCALL_CONTROL_COMMAND_NOTIFY_VIDEO_ON:
      //   console.log('对方打开了摄像头')
      //   break
      // // NETCALL_CONTROL_COMMAND_NOTIFY_VIDEO_OFF 通知对方自己关闭了视频
      // case Netcall.NETCALL_CONTROL_COMMAND_NOTIFY_VIDEO_OFF:
      //   console.log('对方关闭了摄像头')
      //   break;
      // NETCALL_CONTROL_COMMAND_SWITCH_AUDIO_TO_VIDEO_REJECT 拒绝从音频切换到视频
      // case Netcall.NETCALL_CONTROL_COMMAND_SWITCH_AUDIO_TO_VIDEO_REJECT:
      //   console.log("对方拒绝从音频切换到视频通话");
      //   break;
      // NETCALL_CONTROL_COMMAND_SWITCH_AUDIO_TO_VIDEO 请求从音频切换到视频
      case WebRTC.NETCALL_CONTROL_COMMAND_SWITCH_AUDIO_TO_VIDEO:
        console.log('对方请求从音频切换到视频通话')
        this.switchToVideo()
        break
      // // NETCALL_CONTROL_COMMAND_SWITCH_AUDIO_TO_VIDEO_AGREE 同意从音频切换到视频
      // case Netcall.NETCALL_CONTROL_COMMAND_SWITCH_AUDIO_TO_VIDEO_AGREE:
      //   console.log("对方同意从音频切换到视频通话");
      //   if (this.requestSwitchToVideoWaiting) {
      //     this.doSwitchToVideo();
      //   }
      //   break;
      // NETCALL_CONTROL_COMMAND_SWITCH_VIDEO_TO_AUDIO 从视频切换到音频
      case WebRTC.NETCALL_CONTROL_COMMAND_SWITCH_VIDEO_TO_AUDIO:
        console.log('对方请求从视频切换为音频')
        this.switchToAudio()
        break
      // NETCALL_CONTROL_COMMAND_BUSY 占线
      case WebRTC.NETCALL_CONTROL_COMMAND_BUSY:
        console.log('对方正在通话中')
        console.log('取消通话')
        this.webrtc.hangup()
        // this.resetWhenHangup()
        this.clearCallTimer()
        this.isBusy = true
        this.sendLocalMessage('对方正在通话中，取消呼叫', obj.account)
        if (this.afterPlayRingA) {
          this.afterPlayRingA = function () {
            this.playRing('C', 3, function () {
              this.vue.$Message.error('对方正在通话中')
              this.cancelCalling()
            }.bind(this))
          }.bind(this)
        } else {
          this.clearRingPlay()
          this.playRing('C', 3, function () {
            this.vue.$Message.error('对方正在通话中')
            this.cancelCalling()
          }.bind(this))
        }
        break
      // NETCALL_CONTROL_COMMAND_SELF_CAMERA_INVALID 自己的摄像头不可用
      // case Netcall.NETCALL_CONTROL_COMMAND_SELF_CAMERA_INVALID:
      //   console.log("对方摄像头不可用");
      //   this.$videoRemoteBox.toggleClass("empty", true).find(".message").text("对方摄像头不可用");
      //   if (this.isRtcSupported) {
      //     //p2p
      //     if (this.yx.crtSessionType === 'p2p') {
      //       return this.stopRemoteStream();
      //     }
      //     // team
      //     this.stopRemoteStreamMeeting(obj.account);
      //   }
      //   break;
      // NETCALL_CONTROL_COMMAND_SELF_ON_BACKGROUND 自己处于后台
      // NETCALL_CONTROL_COMMAND_START_NOTIFY_RECEIVED 告诉发送方自己已经收到请求了（用于通知发送方开始播放提示音）
      // NETCALL_CONTROL_COMMAND_NOTIFY_RECORD_START 通知对方自己开始录制视频了
      // NETCALL_CONTROL_COMMAND_NOTIFY_RECORD_STOP 通知对方自己结束录制视频了
    }
  },
  onCallerAckSync (obj) {
    console.log('onCallerAckSync:其它端已处理')
    if (this.beCalledInfo && obj.channelId === this.beCalledInfo.channelId) {
      this.sendLocalMessage('其它端已处理')
      this.clearRingPlay()
      setTimeout(() => {
        this.resetWhenHangup()
      }, 1000)
    }
  },
  sendLocalMessage (msg, to) {
    this.store.dispatch('imchat/sendLocalMessage', msg, to)
  },
  switchToVideo () {
    this.webrtc.control({
      command: WebRTC.NETCALL_CONTROL_COMMAND_SWITCH_AUDIO_TO_VIDEO_AGREE
    })
    this.webrtc.switchAudioToVideo()
    this.callType = WebRTC.NETCALL_TYPE_VIDEO
    setTimeout(() => {
      this.startRemoteStream()
    }, 100)
  },
  switchToAudio () {
    this.webrtc.switchVideoToAudio()
    this.callType = WebRTC.NETCALL_TYPE_AUDIO
    this.vue.$Message.warning({
      content: '对方已结束屏幕共享',
      duration: 5
    })
    setTimeout(() => {
      this.stopRemoteStream()
    }, 100)
  },
  startRemoteStream (obj) {
    obj = obj || {}
    obj.node = obj.node
    console.log('开启远端流显示 startRemoteStream')
    try {
      this.store.commit('imchat/setVideoCall', true)
      setTimeout(() => {
        this.webrtc.startRemoteStream(obj)
        this.setRemoteVideoSize()
      }, 500)
    } catch (e) {
      console.log('开启远端流失败', e)
    }
  },
  stopRemoteStream () {
    console.log('停止远端流显示 stopRemoteStream')
    try {
      this.webrtc.stopRemoteStream()
      this.store.commit('imchat/setVideoCall', false)
    } catch (e) {
      console.log('停止远端流失败')
      console && console.warn && console.warn(e)
    }
  },
  setRemoteVideoSize () {
    this.webrtc.setVideoViewRemoteSize({
      cut: true,
      width: 375,
      height: 667
    })
  }
}
