<template>
  <div class="im-list">
    <div class="im-search">
      <input v-model="filterName" type="text" class="search-input" placeholder="搜索" />
      <i class="filter-icon el-icon-user user-info" title="顾问信息" @click="editInfo" />
      <i
        :class="[filterUnread ? 'icon-xinxiang' :'icon-xinxianggray', {'flash': remind && !filterUnread}]"
        class="filter-icon unread iconfont"
        title="筛选未读"
        @click="unreadFilter"
      />
      <i
        :class="[filterStar ? 'el-icon-star-on' : 'el-icon-star-off']"
        class="filter-icon star"
        title="筛选星标"
        @click="starFilter"
      />
    </div>
    <div class="user-list user-list-oc">
      <!-- <div class="user-list" :class="{'user-list-oc': crmRoleId === 8}">  TODO 0424-->
      <template v-if="sessionlist.length">
        <div
          v-for="session in sessionlist"
          v-show="!!session.name"
          :key="session.id"
          :class="{'active': currSessionId === session.id}"
          class="user-name"
          @click="setCurrSession(session)"
        >
<!--          <el-tag-->
<!--            type="danger"-->
<!--            effect="dark"-->
<!--            size="mini"-->
<!--            v-if="(userInfos[session.to] && userInfos[session.to].status === 2)"-->
<!--          >已解绑</el-tag>-->
          <el-tag
            type="warning"
            effect="dark"
            size="mini"
            v-if="(userInfos[session.to] && userInfos[session.to].consultLabel === 1)"
          >调出</el-tag>
          <el-tag
            type="warning"
            effect="dark"
            size="mini"
            v-if="(userInfos[session.to] && userInfos[session.to].consultLabel === 2)"
          >调度</el-tag>
          <el-tag
            type="warning"
            effect="dark"
            size="mini"
            v-if="(userInfos[session.to] && userInfos[session.to].consultLabel === 4)"
          >委托</el-tag>
          <el-tag
            type="warning"
            effect="dark"
            size="mini"
            v-if="(userInfos[session.to] && userInfos[session.to].consultLabel === 5)"
          >代班</el-tag>
          <el-link
            class="user-name-content"
            :class="{'unBind': ((userInfos[session.to] && userInfos[session.to].status !== 2))}"
            :underline="false"
            :title="session.name"
          >{{ session.name }}</el-link>
          <el-badge v-show="session.unread > 0" :value="session.unread" :max="99" class="item" />
          <i
            :class="session.isMute ? 'el-icon-star-on' : 'el-icon-star-off'"
            class="el-message__star"
            @click.stop="changeMark($event, session.id)"
          />
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import util from '@/common/util'
import localStorage from '@/common/utils/localstorage'
// import { getStudentContactDetail } from '../../api/advisor'
export default {
  data () {
    return {
      filterName: '',
      filterUnread: false,
      filterStar: false,
      session: {}
    }
  },
  computed: {
    currSessionId () {
      return this.$store.state.imchat.currSessionId
    },
    userInfos () {
      return this.$store.state.imchat.userInfos
    },
    syncedNames () {
      const userId = this.$store.userUID
      return localStorage.get(userId)
    },
    remind () {
      // const msgs = this.$store.state.imchat.msgs
      const unreadList = this.$store.state.imchat.currShowSessionList.filter(item => {
        if (item.scene === 'p2p' || /^p2p-/.test(item.id)) {
          const to = item.to || item.id.replace(/^p2p-/, '')
          if (to === this.myUserId) return false
        }
        // const sessionMsgList = msgs[item.id] || []
        // const lastMsg =
        //   sessionMsgList[sessionMsgList.length - 1] || item.lastMsg || {}
        // if (item.unread > 0 && lastMsg.flow === 'in') {
        if (item.unread > 0) {
          return true
        } else {
          return false
        }
      })
      return unreadList.length > 0
    },
    sessionlist () {
      const msgs = this.$store.state.imchat.msgs
      const stateSessionList = [...this.$store.state.imchat.sessionlist]
      const originSessionList = [...this.$store.state.imchat.originSessionList]
      const bindStar = [] // 已绑定 星标有未读
      const bindStarRead = [] // 已绑定 星标
      const bind = [] // 已绑定 未读
      const bindRead = [] // 已绑定 已读
      const unBindStar = [] // 未绑定 星标有未读
      const unBindStarRead = [] // 未绑定 星标
      const unBind = [] // 未绑定 未读
      const unBindRead = [] // 未绑定 已读
      originSessionList.forEach(originSession => {
        stateSessionList.forEach(stateSession => {
          if (originSession.nimAccid === stateSession.to) {
            if (this.crmRoleId === 8) {
              if (originSession.status === 1) {
                if (stateSession.isMute) {
                  if (stateSession.unread > 0) {
                    bindStar.push(stateSession)
                  } else {
                    bindStarRead.push(stateSession)
                  }
                } else if (stateSession.unread > 0) {
                  bind.push(stateSession)
                } else {
                  bindRead.push(stateSession)
                }
              } else {
                if (stateSession.isMute) {
                  unBindStar.push(stateSession)
                } else if (stateSession.unread > 0) {
                  unBind.push(stateSession)
                } else {
                  unBindRead.push(stateSession)
                }
              }
            } else {
              // 是否星标
              if (stateSession.isMute) {
                if (stateSession.unread > 0) {
                  bindStar.push(stateSession)
                } else {
                  bindStarRead.push(stateSession)
                }
                // 是否有未读
              } else if (stateSession.unread > 0) {
                bind.push(stateSession)
                // 其他情况
              } else {
                bindRead.push(stateSession)
              }
            }
          }
        })
      })
      let _session = []
      const hiddenSessionId = Object.keys(this.userInfos).filter(accid => this.userInfos[accid].consultLabel === 3 || this.userInfos[accid].consultLabel === 6).map(accid => 'p2p-' + accid) // 找出调入又回调的学员sessionId
      if (this.crmRoleId === 8) {
        _session = [
          ...bindStar,
          ...bindStarRead,
          ...bind,
          ...bindRead,
          ...unBindStar,
          ...unBindStarRead,
          ...unBind,
          ...unBindRead
        ]
      } else if (this.crmRoleId === 5) {
        _session = stateSessionList.filter(session => !hiddenSessionId.includes(session.id)) // 过滤掉这些学员
      }
      const sessionList = _session.filter(item => {
        item.name = ''
        if (item.scene === 'p2p' || /^p2p-/.test(item.id)) {
          const to = item.to || item.id.replace(/^p2p-/, '')
          let userInfo = null
          if (to !== this.myUserId) {
            userInfo = this.userInfos[to]
          } else {
            return false
          }
          if (userInfo) {
            item.name = util.getFriendAlias(userInfo)
            const syncedNames = this.syncedNames || {}
            item.name === to &&
              (item.name = syncedNames[to] ? syncedNames.alias : item.name)
            item.isMute = userInfo.isMute
          }
        }
        // 此处三个过滤条件应该设置为互斥的，不然会导致结果不全！！！
        if (this.filterName) {
          if (item.name.indexOf(this.filterName) > -1) {
            return true
          } else {
            return false
          }
        }
        if (this.filterUnread) {
          const sessionMsgList = msgs[item.id] || []
          const lastMsg =
            sessionMsgList[sessionMsgList.length - 1] || item.lastMsg || {}
          if (item.unread > 0 || lastMsg.flow === 'in') {
            return true
          } else {
            return false
          }
        }
        if (this.filterStar) {
          if (item.isMute) {
            return true
          } else {
            return false
          }
        }
        return true
      })
      // eslint-disable-next-line vue/no-async-in-computed-properties
      setTimeout(() => { // 如果currSessionId需要隐藏，加载其他的session
        if (this.currSessionId && hiddenSessionId.includes(this.currSessionId)) {
          this.$store.dispatch('imchat/acceptCallToSession', sessionList[0].id.replace(/^p2p-/, ''))
        }
      }, 500)
      this.$store.commit('imchat/setCurrShowSessionList', sessionList)
      return sessionList
    },
    myUserId () {
      return `${this.$store.state.imchat.userUID}`
    },
    crmRoleId () {
      return this.$store.state.roleRelativeData.userInfo.crmRoleId
    }
  },
  watch: {
    filterName (newVal) {
      if (newVal) {
        this.filterUnread && (this.filterUnread = false)
        this.filterStar && (this.filterStar = false)
      }
    }
  },
  methods: {
    editInfo () {
      this.$emit('editInfo')
    },
    setCurrSession (session) {
      if (!session) {
        session = this.session
      } else {
        this.session = session
      }
      if (session && session.id) {
        this.$store.dispatch('imchat/setCurrSession', session.id)
        this.$store.dispatch('imchat/getStuPhoneAndNum', session.to)
        // let oid = session.id.replace('p2p-', '')
        this.$store.dispatch(
          'imchat/setStudentContactDetail',
          this.userInfos[session.to]
        )
        // if (this.crmRoleId) {
        // // if (this.crmRoleId === 8) { TODO 0424
        //   this.$store.dispatch('imchat/setStudentContactDetail', this.userInfos[session.to])
        // } else {
        //   this.$store.commit('imchat/currImTitleInfo', this.$store.state.imchat.userInfos[oid])
        // }
      }
    },
    unreadFilter () {
      this.filterUnread = !this.filterUnread
      this.filterStar && (this.filterStar = false)
      this.filterName && (this.filterName = '')
    },
    starFilter () {
      this.filterStar = !this.filterStar
      this.filterUnread = this.filterUnread = false
      this.filterName && (this.filterName = '')
    },
    changeMark (event, account) {
      const userInfos = this.$store.state.imchat.userInfos
      const currUser = userInfos[account.replace(/^p2p-/, '')] || {}
      this.$store.dispatch('imchat/updateMute', {
        account,
        isMute: !currUser.isMute
      })
    }
  }
}
</script>

<style lang="less" scoped>
.im-list {
  border-right: 1px solid #ccc;
  position: relative;
  width: 240px;
  box-sizing: border-box;
  .im-search {
    padding: 5px;
    height: 40px;
    border-bottom: 1px solid #ccc;
    box-shadow: 0 0 10px #ccc;
    position: relative;
    z-index: 1;
    .search-input {
      display: block;
      width: 50%;
      height: 26px;
      padding: 6px 12px;
      font-size: 14px;
      line-height: 1.42857143;
      color: #555555;
      background-color: #fff;
      background-image: none;
      border: 1px solid #ccc;
      border-radius: 4px;
      box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
      transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
    }
    .filter-icon {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      cursor: pointer;
      color: #b4bccc;
      font-size: 25px;
    }
    .star {
      right: 5px;
      font-size: 28px;
    }
    .unread {
      right: 35px;
    }
    .user-info {
      right: 65px;
    }
  }
  .user-list {
    height: 355px;
    overflow: auto;
    &.user-list-oc {
      height: 450px;
    }
  }
  .user-name {
    position: relative;
    display: block;
    line-height: 35px;
    height: 35px;
    border-bottom: 1px solid #ccc;
    padding: 0 15px;
    .user-name-content {
      /deep/ span {
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        max-width: 7em;
      }
      &.unBind {
        /deep/ span {
          max-width: 12em;
        }
      }
    }
    &:hover {
      text-decoration: none;
      background: #eee;
      cursor: pointer;
    }
    &.active {
      background: #eee;
    }
  }
}
.el-message__star {
  position: absolute;
  top: 50%;
  right: 5px;
  transform: translateY(-50%);
  cursor: pointer;
  color: #b4bccc;
  font-size: 26px;
}
.im-unread {
  background: #d66262;
  color: #fff;
  padding: 0 5px;
  border-radius: 10px;
  font-style: normal;
  font-size: 12px;
}

@keyframes twinkling {
  0%,
  50%,
  100% {
    opacity: 1;
  }
  25%,
  75% {
    opacity: 0;
  }
}
.flash {
  animation: twinkling 1s 0.2s ease both infinite;
}
</style>
