<template>
  <Layout style="height: 100%;position:relative" class="main" id="main">
    <Sider
      collapsible
      :width="220"
      :collapsed-width="64"
      v-model="collapsed"
      class="tal-Sider left-sider"
      :style="{overflow: 'hidden'}"
    >
      <side-menu
        accordion
        ref="sideMenu"
        :active-name="$route.name"
        :collapsed="collapsed"
        @on-select="turnToPage"
        :menu-list="menuList"
        :userInfo="userInfo"
      >
        <section
          v-show="!collapsed"
          style="text-align: center;font-size: 20px;line-height: 39px"
        >盘古平台</section>
      </side-menu>
    </Sider>
    <Layout>
      <Content class="main-content-con">
        <Layout class="main-layout-con">
          <div class="tag-nav-wrapper">
            <tags-nav
              :value="$route"
              @input="handleClick"
              :list="tagNavList"
              :roleList="roleList"
              :userInfo="userInfo"
              @on-close="handleCloseTag"
            />
          </div>
          <Content class="content-wrapper">
            <TalCall ref="callOutside"
                     v-if="udcCallShow"
                     :class="{ 'udc-calling': udcCalling }"
                     :app-id="callConfig.appId"
                     :signature="callConfig.signature"
                     :timestamp="callConfig.timestamp"
                     :tal-no="$store.state.user.userInfo.empNo"
                     @initSuccess="setCall"
                     @callStateChange="callStateChange"
                     platform="splat"></TalCall>
            <keep-alive :include="cacheList">
              <router-view />
            </keep-alive>
            <keep-alive>
              <ul class="flex flex1" v-show="!!$route.meta.type && $route.meta.type === 'multi'">
                <li
                  class="flex flex1"
                  v-for="item in iframeNav"
                  v-show="item.path === $route.path"
                  :key="item.path"
                >
                  <component :is="item.meta.fakerComponent"></component>
                </li>
              </ul>
            </keep-alive>
            <ABackTop :height="100" :bottom="80" :right="50" container=".content-wrapper"></ABackTop>
            <div id="feedback-draggable" class="feedback" @click="openFeedBack">
              <span>优化</span>
              <span>建议</span>
            </div>
<!--            <im class="im" v-if="ifConsultantRole" />-->
          </Content>
        </Layout>
      </Content>
    </Layout>
    <Modal
      :mask-closable="false"
      title="优化建议"
      v-model="feedbackModal"
      width="1160px"
      :footer-hide="true"
      ref="form"
    >
      <feedback
        v-if="feedbackModal"
        :roleList="roleList"
        :userInfo="userInfo"
        @close="feedbackModal=false"
      ></feedback>
    </Modal>
    <BeCall v-if="beCalling" class="be-call"></BeCall>
    <Calling v-if="calling" class="calling"></Calling>
    <Call v-if="callOther" class="calling"></Call>
    <VideoCall v-show="videoCall" class="video-call"></VideoCall>
  </Layout>
</template>
<script>
import SideMenu from './components/side-menu'
import HeaderBar from './components/header-bar'
import TagsNav from './components/tags-nav'
import User from './components/user'
import ABackTop from './components/a-back-top'
import Fullscreen from './components/fullscreen'
import Language from './components/language'
import FloatIcons from './components/s-icons'
import Feedback from '../feedback'
import Im from '../imchat/im'
import BeCall from '../imchat/BeCall'
import Call from '../imchat/Call'
import Calling from '../imchat/Calling'
import VideoCall from '../imchat/VideoCall'
// import ErrorStore from './components/error-store'
import { mapMutations, mapActions, mapGetters } from 'vuex'
import { routeEqual } from '@/libs/util'
import {
  browser
} from '@/utils/utils'
import routers from '@/router/routers'
// import componentMixin from '../../router/compontMixin'
import './main.less'

export default {
  name: 'Main',
  // mixins: [componentMixin],
  components: {
    SideMenu,
    HeaderBar,
    Language,
    TagsNav,
    Fullscreen,
    // ErrorStore,
    User,
    ABackTop,
    FloatIcons,
    Feedback,
    Im,
    BeCall,
    Call,
    Calling,
    VideoCall
  },
  data () {
    return {
      collapsed: false,
      feedbackModal: false,
      udcCalling: false
    }
  },
  computed: {
    callConfig () {
      return this.$store.state.app.callOutSideConfig
    },
    udcCallShow () { // 外呼组件是否显示
      return this.$store.state.roleRelativeData.roleIds.includes(this.userInfo.crmRoleId)
    },
    // ...mapState('app', ['callOutSideConfig']),
    ...mapGetters([
      'tagNavList'
    ]),
    tagNavList () {
      return this.$store.state.app.tagNavList
    },
    iframeNav () {
      return this.tagNavList.filter(item => item.meta.type === 'multi')
    },
    // userAvator () {
    //   return this.$store.state.user.avatorImgPath
    // },
    cacheList () {
      const list = ['ParentView', ...this.tagNavList.length ? this.tagNavList.filter(item => !(item.meta && item.meta.notCache)).map(item => item.name) : []]
      return list
    },
    menuList () {
      return this.$store.getters.menuList
    },
    // local () {
    //   return this.$store.state.app.local
    // },
    roleList () {
      return this.$store.state.roleRelativeData.roleList
    },
    userInfo () {
      return this.$store.state.roleRelativeData.userInfo
    },
    // 判断当前角色是否为顾问
    ifConsultantRole () {
      let crmRoleId = this.$store.state.roleRelativeData.userInfo.crmRoleId
      // && ((top !== window) || runEnv() === 'dev')
      return (crmRoleId === 5 || crmRoleId === 8)
    },
    beCalling () {
      return this.$store.state.imchat.beCalling
    },
    calling () {
      return this.$store.state.imchat.calling
    },
    callOther () {
      return this.$store.state.imchat.callOther
    },
    videoCall () {
      return this.$store.state.imchat.videoCall
    }
  },
  methods: {
    ...mapMutations([
      // 'setBreadCrumb',
      'setTagNavList',
      'addTag',
      'setLocal',
      'setHomeRoute',
      'closeTag',
      'setCallInstance'
    ]),
    ...mapActions([
      // 'getMenuList'
      // 'handleLogin',
      // 'getUnreadMessageCount'
    ]),
    turnToPage (route) {
      let { name, params, query } = {}
      if (typeof route === 'string') name = route
      else {
        name = route.name
        params = route.params
        query = route.query
      }
      if (name.indexOf('isTurnByHref_') > -1) {
        window.open(name.split('_')[1])
        return
      }
      const _d = new Date().getTime()
      if (browser().ie && (name === 'applyWorkbenchForMulti' || name === 'applyWorkbench')) {
        top.open(`/service/applyNW?rn=${_d}`)
      } else {
        this.$router.push({
          name,
          params,
          query
        })
      }
    },
    openFeedBack () {
      if (!window.pangu_drag) {
        this.feedbackModal = true
      } else {
        window.pangu_drag = false
      }
    },
    handleCollapsedChange (state) {
      this.collapsed = state
    },
    handleCloseTag (res, type, route) {
      if (type !== 'others') {
        if (type === 'all') {
          this.turnToPage(this.$config.homeName)
        } else {
          if (routeEqual(this.$route, route)) {
            this.closeTag(route)
          }
        }
      }
      this.setTagNavList(res)
    },
    handleClick (item) {
      this.turnToPage(item)
    },
    setCall () {
      this.setCallInstance(this.$refs.callOutside)
    },
    callStateChange (action) {
      switch (action) {
        case 'calling':
        case 'seat.ring':
        case 'customer.ring':
        case 'customer.pickup':
          this.udcCalling = true
          break
        case 'customer.hangup':
        case 'customer.fail':
        case 'seat.handup':
        case 'seat.cancelCall':
          this.udcCalling = false
          break
        default:
          console.log('未知事件')
      }
    }
  },
  watch: {
    '$route' (newRoute) {
      // console.log('newRoute',newRoute)
      const { name, query, params, meta, path } = newRoute
      this.addTag({
        route: { name, query, params, meta, path },
        type: 'push'
      })
      // this.setBreadCrumb(newRoute)
      // this.setTagNavList(getNewTagList(this.tagNavList, newRoute))
      // this.$refs.sideMenu.updateOpenName(newRoute.name)
    }
  },
  mounted () {
    /**
       * @description 初始化设置面包屑导航和标签导航
       */
    this.setTagNavList() // 目前只是在一站式内部使用，如果单独访问的话，需要把这个放开。
    this.setHomeRoute(routers)
    // this.getMenuList()
    const { name, params, query, meta, path } = this.$route
    this.addTag({
      route: { name, params, query, meta, path }
    })
    // this.setBreadCrumb(this.$route)
    // 设置初始语言
    this.setLocal(this.$i18n.locale)
    // 如果当前打开页面不在标签栏中，跳到homeName页
    if (!this.tagNavList.find(item => item.name === this.$route.name)) {
      this.$router.push({
        name: this.$config.homeName
      })
    }
    // 获取未读消息条数
    // this.getUnreadMessageCount()

    // 获取菜单树
    // if (this.$store.getters.userInfo.isAdmin !== 1) {
    //   this.$store.dispatch('queryUserAllowMenuList').then(() => {
    //   }).catch((err) => {
    //     this.$Message.warning({
    //       content: '获取菜单失败',
    //       duration: 5
    //     })
    //   })
    // }
    this.$store.dispatch('queryUserAllowMenuList').then(() => {

    }).catch(() => {
      this.$Message.warning({
        content: '获取菜单失败',
        duration: 5
      })
    })

    this.$store.dispatch('queryUserAllowButtonList').then(() => {

    }).catch(() => {
      this.$Message.warning({
        content: '获取按钮权限列表失败',
        duration: 5
      })
    })

    // 多浏览器打开时切换身份需要让其他页签刷新重置身份
    if (window.addEventListener) {
      window.addEventListener('storage', (e) => {
        if (e.key === 'authIdentityId' && e.newValue !== this.userInfo.authIdentityId) {
          window.location.reload()
        }
      })
    } else {
      window.attachEvent('onstorage', (e) => {
        if (e.key === 'authIdentityId' && e.newValue !== this.userInfo.authIdentityId) {
          window.location.reload()
        }
      })
    }
  }
}
</script>
<style lang="less">
  .be-call,
  .calling,
  .video-call {
    position: absolute;
    right: 200px;
    bottom: 200px;
  }

  .video-call {
    position: absolute;
    right: 550px;
    bottom: 10px;
  }
  .udc-calling {
    background: lightyellow;
  }
</style>
