<template>
  <Submenu :name="`${parentName}`">
    <template slot="title">
      <common-icon :type="parentItem.icon || ''"/>
      <template v-if="parentItem.meta.hasDot && $store.getters.getSideMenuTaskByRouterName(parentItem.name) > 0">
        <Badge dot>
          <span>{{ showTitle(parentItem) }}</span>
        </Badge>
      </template>
      <template v-else>
        <span>{{ showTitle(parentItem) }}</span>
      </template>
    </template>
    <template v-for="item in children">
      <template v-if="item.children && item.children.length === 1">
        <side-menu-item v-if="showChildren(item)" :key="`menu-${item.name}`" :parent-item="item"></side-menu-item>
        <menu-item v-else :name="getNameOrHref(item, true)" :key="`menu-${item.children[0].name}`">
          <!--<common-icon :type="item.children[0].icon || ''"/>-->
          <span>{{ showTitle(item.children[0]) }}</span>
          <span v-if="item.meta.showMulti" class="spas" @click.stop="handleIframe(item)" :key="item.name"><Icon type="md-add-circle" /></span>
        </menu-item>
      </template>
      <template v-else>
        <side-menu-item v-if="showChildren(item)" :key="`menu-${item.name}`" :parent-item="item"></side-menu-item>
        <menu-item v-else :name="getNameOrHref(item)" :key="`menu-${item.name}`">
          <!--<common-icon :type="item.icon || ''"/>-->
          <Badge v-if="item.meta.hasCount && $store.getters.getSideMenuTaskByRouterName(parentItem.name) > 0" :count="$store.getters.getSideMenuTaskByRouterName(parentItem.name)" :offset="[0, -10]">
            <span>{{ showTitle(item) }}</span>
          </Badge>
          <span v-else>{{ showTitle(item) }}</span>
          <span v-if="item.meta.showMulti" class="spas" @click.stop="handleIframe(item)" :key="item.name"><Icon type="md-add-circle" /></span>
        </menu-item>
      </template>
    </template>
  </Submenu>
</template>
<script>
import mixin from './mixin'
import itemMixin from './item-mixin'
import { browser } from '@/utils/utils'
export default {
  name: 'SideMenuItem',
  mixins: [mixin, itemMixin],
  methods: {
    handleIframe (item) {
      const _d = new Date().getTime()
      if (
        browser().ie &&
        (item.name === 'applyWorkbenchForMulti' ||
          item.name === 'applyWorkbench')
      ) {
        top.open(`/service/applyNW?rn=${_d}`)
      } else {
        this.$router.push({ name: `${item.name}ForMulti`, params: { id: _d } })
      }
    }
  }
}
</script>
