<template>
  <Poptip
    transfer
    placement="bottom"
    class="notice-pop"
    @on-popper-show="noticePopOpen = true"
    @on-popper-hide="noticePopOpen = false"
    width="300">
    <Badge style="cursor: pointer;" :count="messageCount" overflow-count="99">
      <span class="iconfont icon-lingdang icon-style" title="通知消息"></span>
    </Badge>
    <div slot="content" class="notice-pop-content">
      <tal-notification-list
        v-if="noticePopShow"
        :show="noticePopOpen"
        :frequency="30000"
        primary-color="#6EB312"
        @on-change-number="newMessageHandel"
        @on-get-all-messages="toNoticeList"
        @on-click-item="messageHandel"
        @on-token-error="getNoticeToken">
      </tal-notification-list>
    </div>
  </Poptip>
</template>

<script>
import { TalNotificationList } from '@peiyou/tal-element'
import { noticePlatLogin } from '@/api/bd'
import { genWakeup } from '@/api/wxmanage'
import sideMenuTasksMixin from '../../sideMenuTasksMixin'
export default {
  name: 'Notice',
  mixins: [sideMenuTasksMixin],
  components: {
    TalNotificationList
  },
  data () {
    return {
      messageCount: 0,
      noticePopShow: false,
      noticePopOpen: false,
      time: 0
    }
  },
  mounted () {
    if (sessionStorage.getItem('X-Ca-Notice-Token')) {
      this.noticePopShow = true
    } else {
      this.getNoticeToken()
    }
  },
  methods: {
    getNoticeToken () {
      noticePlatLogin().then(data => {
        sessionStorage.setItem('X-Ca-Notice-Token', data.token)
        this.$store.commit('setNoticeToken', data.token)
        this.noticePopShow = true
      }).catch(err => {
        this.$Message.error({
          content: '通知平台登录失败：' + err.message
        })
      })
    },
    // 点击每条消息的处理逻辑
    messageHandel (data = {}) {
      if (data.type === 'DiagnosisMsg') {
        this.$router.push({
          name: `scatteredDiagnosisMgtMulti`,
          params: { id: Date.now() },
          query: {
            ...data
          }
        })
        return
      }
      if (data.type === 'WxStatusChange') {
        const interval = new Date().getTime() - this.time
        if (interval > 1000) {
          console.log('超过一秒钟，触发！')
          this.$store.dispatch('initWxInterval')
          this.time = new Date().getTime()
        } else {
          console.log('频繁触发，已丢弃！')
        }
        return
      }
      genWakeup(data).then(data => {
        const int = setTimeout(() => {
          window.open(data.url)
          clearTimeout(int)
        }, 500)
      }).catch(err => {
        this.$Message.error({
          content: '跳转天策失败：' + err.message
        })
      })

      // sideMenuTasksMixin
      // 侧边栏待处理任务提醒
      this.pushTask(data)
    },
    // 去通知中心列表页面
    toNoticeList () {
      this.$router.push({
        name: 'noticeCenter'
      })
    },
    // 新消息通知
    newMessageHandel (val) {
      this.messageCount = val || 0
    }
  }
}
</script>

