.notice-pop {
  display: inline-block;
  .ivu-tabs-mini .ivu-tabs-tab {
    padding: 8px;
  }
  .ivu-poptip-rel {
    .link {
      vertical-align: middle;
      width: 26px;
      height: 26px;
    }
  }
  .ivu-poptip-body-content {
    overflow: initial;
  }
  .ivu-poptip-arrow {
    display: none !important;
  }
  .notice-pop-content {
    word-break: break-all;
    white-space: normal;
    max-height: 600px;
    min-height: 350px;
  }
}
</style>
