// module user用户级按钮权限  roleRelativeData 角色级按钮权限

// this.$haveAuthority('roleRelativeData', 'apply/specialRefundApply/refundableAmount')
Vue.prototype.$haveAuthority = (module, route) => {
  if (!route) {
    return false
  }
  let name;
  if (typeof route === 'string') {
    name = route
    return store.state[module].allowButtonList.indexOf(name) > -1
  } else {
    if (route.meta && route.meta.noAccess) {
      return true
    } else {
      name = route.name
      return store.state[module].allowButtonList.indexOf(name) > -1
    }
  }
}




