<template>
  <div style="margin: 10px">
    <Form label-position="top" ref="formValidate" :model="formData" :rules="ruleValidate">
      <FormItem label="反馈类型：" required prop="type">
        <RadioGroup v-model="formData.type">
          <Radio label="optimize">现有功能优化</Radio>
          <Radio label="new">新增功能建议</Radio>
        </RadioGroup>
      </FormItem>
      <FormItem label="反馈内容:" required prop="content">
        <p>请详细描述问题便于我们定位问题。</p>
        <p>示例1：描述现有功能优化时，简述现有功能使用不便的原因，以及解决后的好处；</p>
        <p style="margin-bottom: 10px">示例2：新增功能建议提出时，请说明提出新功能希望解决的问题；</p>
        <Input v-model="formData.content" :maxlength="200" type="textarea" :autosize="{minRows: 3,maxRows: 5}" placeholder="请输入您的建议，产品经理努力接收中，反馈有惊喜哟～～～"></Input>
      </FormItem>
      <FormItem label="上传图片（不大于5M）:" prop="imgUrl">
        <div class="demo-upload-list" v-for="(item,index) in fileList" :key="index">
          <template v-if="item.status === 'finished'">
            <img :src="item.url+ '?x-oss-process=image/resize,m_fixed,h_100,w_100'">
            <div class="demo-upload-list-cover">
              <Icon type="ios-eye-outline" @click.native="handleView(item.url)"></Icon>
              <Icon type="ios-trash-outline" @click.native="handleRemove(item.fileName)"></Icon>
            </div>
          </template>
          <template v-else>
            <Progress :percent="item.percent" hide-info></Progress>
          </template>
        </div>
        <div class="upload-btn" v-show="fileList.length < 3">
          <Icon id="pickfiles" type="ios-add" size="80"></Icon>
        </div>
      </FormItem>
    </Form>
    <p>如果在使用系统过程中遇到问题，请按以下步骤联系IT支持老师协助解决。</p>
    <img style="width: 500px;margin-top: 20px;" src="../../assets/images/speiyou-itsupport-group-3.png" alt="">
    <Modal title="预览图片" v-model="visible" width="1200">
      <img :src="imgUrl" v-if="visible" style="width: 100%">
      <div slot="footer">
        <Button @click="visible = false">关闭</Button>
      </div>
    </Modal>
    <div style="text-align: right">
      <Button @click="save" type="primary">确认</Button>
      <Button @click="$emit('close')">取消</Button>
    </div>
  </div>
</template>

<script>
import { getCityList } from '@/api/bd'
import { addFeedback } from '@/api/feedback'
import { runEnv } from '@/utils/utils'
import { uploadGetAuth } from '@/api/auth'
export default {
  props: {
    userInfo: Object,
    roleList: Array
  },
  data () {
    return {
      cityList: [],
      formData: {
        type: '',
        content: ''
      },
      ruleValidate: {
        type: [
          { required: true, message: '问题类型不能为空', trigger: 'blur' }
        ],
        content: [
          { required: true, message: '反馈内容不能为空', trigger: 'blur' },
          { message: '反馈内容最多可输入200字', max: 200, trigger: 'blur', type: 'string' }
        ]
      },
      role: {},
      fileList: [],
      uploadUrl: 'http://funsion-video.oss-cn-beijing.aliyuncs.com',
      uploadParams: {},
      imgUrl: '',
      visible: false
    }
  },
  methods: {
    save () {
      this.$refs.formValidate.validate(valid => {
        if (valid) {
          this.formData.content += `\n ${window.navigator.userAgent}`
          addFeedback(Object.assign({}, this.formData, {
            cityCode: this.cityList[0].id,
            serviceCenterId: '1',
            serviceCenterName: '1',
            roleId: this.role.roleId,
            roleName: this.role.roleName,
            imgUrl: this.fileList.map(f => f.url).join(',')
          })).then(data => {
            this.$Message.success({
              content: '提交成功',
              duration: 5
            })
            this.$emit('close')
          }).catch(err => {
            this.$Message.error({
              content: '提交失败：' + err.message,
              duration: 5
            })
          })
        } else {
          this.$Message.warning({
            content: '请完善必填信息',
            duration: 5
          })
        }
      })
    },
    handleView (imgUrl) {
      this.imgUrl = imgUrl
      this.visible = true
    },
    uploadOssImage () {
      // oss
      let that = this
      let uploader = new plupload.Uploader({
        runtimes: 'html5,html4',
        browse_button: 'pickfiles',
        multi_selection: false, // 是否多选
        url: `${runEnv() === 'prod' ? 'https' : 'http'}://oss.aliyuncs.com`,
        filters: {
          mime_types: [ // 只允许上传图片和zip文件
            { title: 'Image files', extensions: 'jpg,jpeg,png' }
          ],
          max_file_size: '5mb', // 最大只能上传5mb的文件
          prevent_duplicates: false // 不允许选取重复文件
        },
        init: {
          PostInit: function () {
            // 当Init事件发生后触发
          },
          FilesAdded: function (up, files) {
            if (files.length > 3) {
              that.$Message.warning({
                content: `最多上传3张图片`,
                duration: 5
              })
              return
            }
            files.forEach(file => {
              // 当文件添加到上传队列后触发
              uploadGetAuth({ fileName: file.name, empNo: that.$store.getters.userInfo.empNo }).then(res => {
                let flag = that.fileList.filter(f => f.fileName === file.name).length > 0
                if (flag) {
                  that.$Message.warning({
                    content: `图片${file.name}已上传，请勿重复上传`,
                    duration: 5
                  })
                  return
                }
                let key = res.dir + that.guid() + '.' + file.name.split('.')[1]
                let new_multipart_params = {
                  'key': key,
                  'policy': res.policy,
                  'expire': res.expire,
                  'OSSAccessKeyId': res.accessid,
                  'success_action_status': '200', // 让服务端返回200,不然，默认会返回204
                  // 'callback': res.callback, // 设置回调
                  'signature': res.signature
                }
                that.fileList.push({
                  url: `${res.host}/${key}`,
                  fileName: file.name,
                  status: 'uploading',
                  percent: 0
                })
                up.setOption({
                  'url': res.host,
                  'multipart_params': new_multipart_params
                })
                up.start()
              }).catch(err => {
                console.log(err)
                up.stop()
                up.removeFile(file)
                that.$Message.warning({
                  content: '文件上传失败，请重新操作',
                  duration: 10
                })
              })
            })
          },
          UploadProgress: function (up, file) {
            let fileItem = that.fileList.filter(f => f.fileName === file.name)[0]
            if (fileItem) {
              fileItem.percent = up.total.percent
            }
          },
          FileUploaded: function (up, file, info) {
            let fileItem = that.fileList.filter(f => f.fileName === file.name)[0]
            if (fileItem) {
              fileItem.status = 'finished'
            }
          },
          Error: function (up, err) {
            // 错误回调
            if (err.code === -600) {
              that.$Message.warning({
                content: '您上传的文件超过大小限制5M',
                duration: 5
              })
            } else if (err.code === -601) {
              that.$Message.warning({
                content: '选择的文件格式错误，目前仅支持jpg,jpeg,png',
                duration: 5
              })
            } else if (err.code === -602) {
              that.$Message.warning({
                content: '这个文件已经上传过一遍了',
                duration: 5
              })
            } else {
              that.$Message.warning({
                content: '上传错误，请稍后再试',
                duration: 5
              })
            }
          }
        }
      })
      uploader.init()
      // oss 结束
    },
    guid () {
      return 'xxxxxxxxxxxx4xxxyxxxxxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
        let r = Math.random() * 16 | 0
        let v = c === 'x' ? r : (r & 0x3 | 0x8)
        return v.toString(16)
      })
    },
    handleRemove (fileName) {
      this.fileList = this.fileList.filter(f => f.fileName !== fileName)
    }
  },
  mounted () {
    this.uploadOssImage()
    getCityList().then(data => {
      this.cityList = data
    }).catch(err => {
      this.$Message.error({
        content: '获取城市失败：' + err.message,
        duration: 5
      })
    })
    this.$nextTick(() => {
      this.role = this.roleList.filter(r => r.identityId === this.userInfo.authIdentityId)[0]
    })
  }
}
</script>

<style scoped>
  .demo-upload-list{
    display: inline-block;
    width: 100px;
    height: 100px;
    text-align: center;
    line-height: 100px;
    border: 1px solid transparent;
    border-radius: 4px;
    overflow: hidden;
    background: #fff;
    position: relative;
    box-shadow: 0 1px 1px rgba(0,0,0,.2);
    margin-right: 4px;
  }
  .demo-upload-list img{
    width: 100%;
    height: 100%;
  }
  .demo-upload-list-cover{
    display: none;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0,0,0,.6);
  }
  .demo-upload-list:hover .demo-upload-list-cover{
    display: block;
  }
  .demo-upload-list-cover i{
    color: #fff;
    font-size: 20px;
    cursor: pointer;
    margin: 0 2px;
  }
  .upload-btn {
    display: inline-block;
    width: 100px;
    height: 100px;
    text-align: center;
    line-height: 100px;
    border: 1px dashed #ccc;
    border-radius: 4px;
    overflow: hidden;
    cursor: pointer;
  }
</style>
