// 天策相关接口
import axios from '../libs/api.request'

/** 获取微信小助手列表及登陆状态
 * @param cityCodecityCode query true string
 * @param uid uid query true string
 */
export const getWxStatus = (params) => {
  return axios.request({
    url: '/wxmanage/list',
    params: params,
    method: 'get'
  })
}

/** 获取微信小助手列表及登陆状态
 * @param cityCodecityCode query true string
 * @param uid uid query true string
 */
export const genWakeup = (params) => {
  return axios.request({
    url: '/wxmanage/genWakeup',
    data: params,
    method: 'POST'
  })
}

/** 触发微信消息通知
 * @param cityCodecityCode query true string
 * @param uid uid query true string
 */
export const noticeTrigger = (params) => {
  return axios.request({
    url: '/wxmanage/noticeTrigger',
    data: params,
    method: 'POST'
  })
}

/**
 * 查询顾问与学员绑定关系
 * @param params
 * @returns {*}
 */
export const bindRelate = params => {
  return axios.request({
    url: '/wxmanage/bindRelate',
    params,
    method: 'get'
  })
}

/**
 * 新增绑定关系 接口
 * @param params
 * @returns {*}
 */
export const bind = params => {
  return axios.request({
    url: '/wxmanage/bind',
    data: params,
    method: 'post'
  })
}
