import XesLoggerSDK from '@xes/xes_fe_log'
import { runEnv } from '@/libs/util'

const env = runEnv()

// 直在仿真和线上环境开启
export default function (Vue) {
  if (env === 'prod' || env === 'beta') {
    const eventid = env === 'prod' ? 'fe-service-platform' : 'fe-service-platform_beta'
    Vue.use(XesLoggerSDK, {
      cacheUploader: {
        interval: 10000,
        batch: 5
      },
      common: { // 需要传自定义参数,请写在common中
        eventid: eventid // 业务自定义的事件字段 例如 huatuoweb
        // userId: '需要用户ID', //自定义参数
        // userPhone: '用户手机号' //自定义参数 如果不需要自定义参数可以不写
      },
      param: {
      },
      clickMsg: {
        open: false
      },
      // 是否自动上报展现日志开关，如果自动上报需要把展现日志必备参数设置到window.courseDatanSerach上，如果不自动上报，则可以在业务想要的地方使用loadMsg方法自主上报展现日志
      loadMsg: {
        open: false
      },
      hashchange: {
        open: true,
        pvid: 'hash',
        loadParam: {
          //   'index': {
          //     event_properties: {
          //       'event_type': 'pv',
          //       'event_id': 'APP',
          //       '1111111': '22222'
          //     }
          //   },
          //   'DMP': {
          //     event_properties: {
          //       'event_type': 'pv',
          //       'event_id': 'DMP',
          //       '1111111': '22222'
          //     }
          //   },
          //   'path/a': { 'key1': 'zzz' }
        }
      },
      network: {
        open: true,
        sample: 1
      },
      runtime: {
        open: true,
        sample: 1
      },
      resource: {
        open: true,
        sample: 1
      },
      performance: {
        open: true,
        sample: 1
      },
      vue: {
        errorHandler: true // vue可以开启，react需要删除
      },
      appid: {
        // 仿真环境
        'one-beta.speiyou.cn': {
          'appid': '1004198'
        },
        'one.speiyou.cn': {
          'appid': '1004198'
        }
      }
    })
  }
}
