import axios from '@/libs/auth.request'

/**
 *
 根据员工编号获取身份列表
 @param 员工编号 empNo
*/
export function getEmpDeptRolesByEmpNo (params) {
  return axios.request({
    url: 'auth/getEmpDeptRolesByEmpNo',
    method: 'get',
    params
  })
}

export function getResourcesByEmpNo (params) {
  return axios.request({
    url: '/resource/getResourcesByEmpNo',
    method: 'get',
    params
  })
}

// 阿里oss 鉴权
export const uploadGetAuth = params => {
  return axios.request({
    url: '/upload/getAuth',
    method: 'get',
    params: params
  })
}

// 获取权限按钮
export const getUserResourceUrlList = params => {
  return axios.request({
    url: '/auth/getUserResourceUrlList',
    method: 'get',
    params: params
  })
}
