import Vue from 'vue'
import Router from 'vue-router'
import routes from './routers'
import store from '@/store'
import iView from 'view-design'
import { getToken, setTitle, sendErrorToParentFrame } from '@/libs/util'
import config from '@/config'
import { viewTrack } from '@/libs/tal-sensors'
const { homeName } = config

Vue.use(Router)
const router = new Router({
  routes,
  mode: 'history', // require service support
  base: '/service/'
})
const LOGIN_PAGE_NAME = 'login'
if (process.env.NODE_ENV === 'development') {
  sessionStorage.setItem('currentEnv', 'dev')
}
// const turnTo = (to, access, next) => {
//   if (!access.length || !access) next({ replace: true, name: 'error_401' })
//   else if (access.indexOf(to.name) >= 0 || to.name === config.homeName) next() // 有权限，可访问
//   else next({ replace: true, name: 'error_401' }) // 无权限，重定向到401页面
//   console.timeEnd('auth')
// }

router.beforeEach((to, from, next) => {
  iView.LoadingBar.start()
  const token = getToken()
  if (['error_401', 'error_404', 'error_500'].indexOf(to.name) >= 0) {
    next()
  } else if (!token) {
    iView.LoadingBar.finish()
    if (to.name === 'error_401') {
      next()
    } else {
      next({
        name: 'error_401'
      })
      // window.parent.postMessage(`HTTP_COOKIE_VALUE_ERROR${config.postMessageTag}`, '*')
      sendErrorToParentFrame(`HTTP_COOKIE_VALUE_ERROR`)
    }
  } else if (token && to.name === LOGIN_PAGE_NAME) {
    // 已登录且要跳转的页面是登录页
    next({
      name: homeName // 跳转到homeName页
    })
  } else if (to.name === 'applyStuArchivesForMulti' && to.hash) { // 解决学员档案加了锚点之后丢失query的问题
    if ((from.query && from.query.card) && (!to.query || !to.query.card)) {
      to = Object.assign({}, to, {
        query: from.query
      })
      next(to)
    } else {
      next()
    }
  } else {
    store.dispatch('getLoginInfo')
    next()
  }
})

router.afterEach(to => {
  try {
    setTitle(to, router.app)
  } catch (e) {
    document.title = config.title
  }
  iView.LoadingBar.finish()
  window.scrollTo(0, 0)
})

// 埋点
viewTrack(router)

// 解决 Avoided redundant navigation to current location
const originalPush = Router.prototype.push
Router.prototype.push = function push (location) {
  return originalPush.call(this, location).catch(err => err)
}

export default router
